import React, { useEffect, useState } from "react";
import { makeStyles, createStyles } from "@material-ui/core/styles";
import { Typography } from "@material-ui/core";
import Grid from '@material-ui/core/Grid';
import Tooltip from '@material-ui/core/Tooltip';
import Container from '@material-ui/core/Container';
import EditWatarCycleGraphModal from '../common_view/EditWatarCycleGraphModal';
import LineChart from './LineChart';
import InsertChartIcon from '@material-ui/icons/InsertChart';
import baseUrl from "../../config/config";
import '../../styles/graph.css';

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
        width: 500,
        display: "flex",
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: 'center',
      color: theme.palette.text.secondary,
    },
    card_margin_top: {
        marginTop: '5px',
        marginBottom: '5px',
    }, 
  }),
);

const useStylesBootstrap = makeStyles((theme) => ({
    arrow: {
      color: theme.palette.common.black,
    },
    tooltip: {
      backgroundColor: theme.palette.common.black,
      fontSize: '15px',
      marginTop: '8px !important',
      marginLeft: '10px !important',
    },
}))

function BootstrapTooltip(props) {
    const classes = useStylesBootstrap();
    return <Tooltip arrow classes={classes} className="text" {...props} />;
}

export default function Graph(props) {

    const classes = useStyles();

    const [sliderHeight, setSliderHeight] = useState(100);
    const [sliderWidth, setSliderWidth] = useState(100);

    const [durationVal, setDurationVal] = useState(0);
    const [hourVal, setHourVal] = useState(0);
    const [isDynamicHour, setIsDynamicHour] = useState(false);

    const [state, setState] = React.useState({
        checkedA: false,
        checkedB: false,
    });

    const [checkedgraphAmanual, setCheckedgraphAmanual] = useState(false);
    const [checkedgraphAauto, setCheckedgraphAauto] = useState(true);
    const [manualWCToggle, setManualWCToggle] = useState();

    const [isActive, setIsActive] = useState(false);

    const [garphAutoData, setGraphAutoData] = useState();
    const [GraphManualData, setGraphManualData] = useState();

    const [open, setOpen] = React.useState(false);

    const [scroll, setScroll] = React.useState('paper');

    useEffect(()=>{
        if(isActive){
            setOpen(true)
            props.setDelayEdit(true)
        }
        else{
            setOpen(false);
        }
    },[isActive]);

    useEffect(()=>{
        setIsActive(props.showGraphIcon)
    },[props.showGraphIcon])

    useEffect(() => {
        var ele = document.getElementById("myCanvas");
        if (ele) {
        setSliderHeight(ele.height - 70);
        setSliderWidth(ele.width);
        }
    }, [document]);

    useEffect(() => {
        if(checkedgraphAmanual !== null) {
            // if (checkedgraphAmanual)
            setCheckedgraphAauto(!checkedgraphAmanual);
        }
    }, [checkedgraphAmanual]);
    
    useEffect(() => {
        if (checkedgraphAauto !== null) {
        setCheckedgraphAmanual(!checkedgraphAauto);
        }
    }, [checkedgraphAauto]);

    const handleClickOpen = (scrollType) => () => {
        setOpen(true);
        props.setDelayEdit(true)
        setScroll(scrollType);
    };

    const descriptionElementRef = React.useRef(null);
    
    React.useEffect(() => {
        if (open) {
            const { current: descriptionElement } = descriptionElementRef;
            if (descriptionElement !== null) {
                descriptionElement.focus();
            }
        }
    }, [open]);

    useEffect(()=>{
        var togglebar = localStorage.getItem('manualWCToggle')
        if(togglebar==='true'){
            setCheckedgraphAmanual(true)
        } else {
            setCheckedgraphAmanual(false)
        }
    },[])

    useEffect(()=>{
        var togglebar = localStorage.setItem('manualWCToggle', checkedgraphAmanual)
    },[checkedgraphAmanual]);

    function fetchGraphAutoData(){
        var token = localStorage.getItem("token");
        var selectedId = localStorage.getItem("selectedId");
        var postGraphAutoData = {
            incubator_id : selectedId,
        };
        fetch(`${baseUrl.baseUrl}water_auto/`, {
            method: "POST",
            headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization: token,
            },
            body: JSON.stringify(postGraphAutoData),
        })
        .then((res) => res.json())
        .then((results) => {
            setGraphAutoData(results);
        });
        }

        useEffect(()=>{
        if(checkedgraphAauto){
            fetchGraphAutoData()
        }
    },[checkedgraphAauto])
 
    function fetchGraphManualData(){
        var token = localStorage.getItem("token");
        var selectedId = localStorage.getItem('selectedId');
        var postGraphManualdata = {
            incubator_id : selectedId,
        };
        fetch(`${baseUrl.baseUrl}water_manual/`, {
            method: "POST",
            headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization: token,
            },
            body: JSON.stringify(postGraphManualdata),
        })
        .then((res) => res.json())
        .then((results) => {
            if(results.error){
                setCheckedgraphAmanual(false)
                setCheckedgraphAauto(true)
            }
            setGraphManualData(results);
        });
    }
 
    useEffect(()=>{
        if(checkedgraphAmanual){
            fetchGraphManualData()
        }
    }, [checkedgraphAmanual])
 
    useEffect(()=>{
        setCheckedgraphAauto(props.inCubeData && props.inCubeData.water_status)
    }, [props.inCubeData])

    return (
        <Container className={classes.card_margin_top}>
            <Grid container>
                <Grid item xs={12} sm={12} md={12} lg={12}>
                    <Typography className="graph_title text">
                        WATERING CYCLES
                    </Typography>
                    <div className={classes.root}>
                        <Typography className="duration_title text">DURATION</Typography>
                        <div className="slider_vertcial">
                            {/* <Slider
                                orientation="vertical"
                                defaultValue={10}
                                aria-labelledby="vertical-slider"
                                max={30}
                                onChange={onChangeDurationHandler}
                            /> */}
                        </div>
                        <div className="graph">
                            <LineChart isDynamicHour={isDynamicHour} hourVal={hourVal} inCubeData={props.inCubeData}/>
                        </div>
                    </div>
                    <div className="slider_line">
                        {/* <Slider
                            orientation="horizontal"
                            defaultValue={21}
                            aria-labelledby="horizontal-slider"
                            min={1}
                            max={21}
                            onChange={onChangeHourHandler}
                        /> */}
                    </div>
                    <Typography className="hour_line text">HOUR</Typography>
                </Grid>
                <Grid item sm={2} md={2} lg={2}>
                    <div className="graph_manual_toggal">
                        <label className="switch">
                            <input type="checkbox"
                                name="checkedA"
                                color="primary"
                                checked={checkedgraphAmanual}
                                onChange={() => {
                                    setCheckedgraphAmanual(!checkedgraphAmanual);
                                }}
                                inputProps={{ "aria-label": "primary checkbox" }}
                            />
                            <span className="sliders round" />
                        </label>
                    </div>
                </Grid>
                <Grid item sm={2} md={2} lg={2}>
                    <div className="graph_manual_title">
                        <Typography className="text" color={checkedgraphAmanual ? "primary" : " "}>
                            MANUAL
                        </Typography>
                    </div>
                </Grid>
                <Grid item sm={2} md={2} lg={2}>
                    <div className="graph_auto_toggal">
                        <label className="switch">
                            <input type="checkbox"
                                name="checkedB"
                                checked={checkedgraphAauto}
                                color="primary"
                                onChange={() => {
                                    setCheckedgraphAauto(!checkedgraphAauto);
                                }}
                                inputProps={{ "aria-label": "primary checkbox" }}
                            />
                            <span className="sliders round" />
                        </label>
                    </div>
                </Grid>
                <Grid item sm={2} md={2} lg={2}>
                    <div className="graph_auto_title">
                        <Typography className="text" color={checkedgraphAauto ? "primary" : ""}>
                            AUTO
                        </Typography>
                    </div>
                </Grid>
                <Grid item sm={2} md={2} lg={2}>
                    {checkedgraphAmanual === true && (
                        <div>
                            <BootstrapTooltip classname="text" title="Edit Program">
                                <div className="edit_btn_color">
                                    <div className="round_edit_icon" onClick={handleClickOpen()}>
                                        <InsertChartIcon className="graph_edit_icon" />
                                    </div>
                                </div>
                            </BootstrapTooltip>
                        </div>
                    )}
                </Grid>
                <EditWatarCycleGraphModal open={open} setOpen={setOpen} inCubeData={props.inCubeData} garphAutoData={garphAutoData} setGraphAutoData={setGraphAutoData} GraphManualData={GraphManualData} setGraphManualData={setGraphManualData} setDelayEdit={props.setDelayEdit} />
            </Grid>
        </Container>
    );
}
