import React, { useEffect, useState } from 'react';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import Header from './Header';
import SideBar from './SideBar';
import CropDetailModal from './CropDetailModal';
import ThreeMonthCropDetail from './ThreeMonthCropDetail';
import DeleteIncubator from './DeleteIncubator';
import RunProgram from './RunProgram';
import NewEntryModal from './NewEntryModal';
import Record from './Record';
import LiveImage from './LiveImage';
import AtmosphereSlider from './AtmosphereSlider';
import LightLevelSlider from './LightLevelSlider';
import Graph from './Graph';
import Spinner from "../Spinner";
import baseUrl from '../../config/config';
import '../../styles/home.css';

const useStyles = makeStyles((theme) =>
    createStyles({
        root: {
            display: 'flex',
        },
        container: {
            paddingTop: theme.spacing(4),
            paddingBottom: theme.spacing(4),
            marginTop: '80px',
        },
        content: {
            flexGrow: 1,
            height: '100vh',
            overflow: 'none !important',
        },
    }),
);

export default function Home(props) {

    const classes = useStyles();

    const [newupdate, setNewUpdate] = useState(0);

    const [selectedID, setSelectedID] = useState('');

    const [updateList, setUpdateList] = useState();

    const [inCubeData, setInCubeData] = useState();

    const [date, setDate] = useState();

    const [selectedName, setSelectedName] = useState();

    const [isRecord, setIsRecord] = useState(false);

    const [disable, setDisable] = useState(false);

    const [deleteBtn, setDeleteBtn] = useState(false);

    const [runBtn, setRunBtn] = useState(false);

    const [openModal, setOpenModal] = useState(false);

    const [cropDetails, setCropDetails] = useState();

    const [showEdit, setShowEdit] = useState(false);

    const [showImage, setShowImage] = useState(false);

    const [delayEdit, setDelayEdit] = useState(false);

    const [showPen, setShowPen] = useState(false);

    const [showAtmPen, setAtmShowPen] = useState(false); 

    const [showLightPen, setLightShowPen] = useState(false);

    const [showGraphIcon, setShowGraphIcon] = useState(false);

    const [loader, setLoader] = React.useState(false);

    const [runData, setRunData] = React.useState({
        atmosphere: {},
        light: {},
        water: {}
    });

    useEffect(() => {
        var sidbar_tab = localStorage.getItem("selectedId");
        if (sidbar_tab) setSelectedID(Number(sidbar_tab));
    }, []);

    useEffect(() => {
        if (!delayEdit) {
            var myClock;
            if (myClock) clearInterval(myClock);
            myClock = setInterval(myTimer, 2000)
            function myTimer() {
                // if(delayEdit) return;
                let sId = localStorage.getItem("selectedId")   
                if (sId !== null)                                
                fetchApi(sId);                                
            }
            return (() => {
                if (myClock) clearInterval(myClock);
            })
        }
    }, [delayEdit])

    useEffect(() => {
    }, [inCubeData])

    useEffect(() => {
        if (selectedID) {
            localStorage.setItem("selectedId", selectedID);
            localStorage.setItem("selectedName", selectedName);
            setLoader(true);
            fetchApi(selectedID)
        }
    }, [selectedID]);

    function fetchApi(selectedID) {
        var token = localStorage.getItem("token");
        fetch(`${baseUrl.baseUrl}incubator/` + selectedID + "/", {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: token,
            },
        })
        .then((res) => res.json())
        .then((results) => {
            if (results && results.error) {
                setInCubeData(' ');
            } else if (results && results.status_key === false) {
                setOpenModal(true);
                setInCubeData(results.msg.results);
            } else {
                setOpenModal(false);
                setInCubeData(results.msg.results);
            }
            if (results.status_key !== false) {
                var tempDate = new Date(results.msg.results.updated_on);
                setDate(tempDate.toUTCString());
            }
            setLoader(false)   
        })
        .catch((rej) => { });
    }

    function fetchCropDetails() {
        setOpenModal(!openModal);
        setShowEdit(true)
        var token = localStorage.getItem("token");
        var selectedId = localStorage.getItem("selectedId");
        var cropDetailData = {
            incubator_id: selectedId,
        };
        fetch(`${baseUrl.baseUrl}crop_detail/`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: token,
            },
            body: JSON.stringify(cropDetailData),
        })
        .then((res) => res.json())
        .then((results) => {
            setCropDetails(results);
        });
    }

    return (
        <div className={classes.root}>
            <CssBaseline />
            <div>
                <Header />
            </div>
            <main className={classes.content}>
                <div className={classes.toolbar} />
                <Container maxWidth="lg" className={classes.container}>
                    <Grid container spacing={3} className="container_top">
                        <Grid item xs={12} md={2} lg={2}>
                            <Card className="card">
                                <CropDetailModal inCubeData={inCubeData} cb={fetchCropDetails} cropDetails={cropDetails} showEdit={showEdit} />
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={2} lg={2}>
                            <Card className="card">
                                <ThreeMonthCropDetail inCubeData={inCubeData} />
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={2} lg={2}>
                            <Card className="card">
                                <NewEntryModal setUpdateList={setUpdateList} setSelectedID={setSelectedID} />
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={2} lg={2}>
                            <Card className="card">
                                <RunProgram runBtn={runBtn} setRunBtn={setRunBtn} inCubeData={inCubeData} setRunData={setRunData} runData={runData} isRecord={isRecord} setIsRecord={setIsRecord} setDelayEdit={setDelayEdit} setAtmShowPen={setAtmShowPen} setLightShowPen={setLightShowPen} />
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={2} lg={2}>
                            <Card className="card">
                                <DeleteIncubator deleteBtn={deleteBtn} setDeleteBtn={setDeleteBtn} inCubeData={inCubeData} setInCubeData={setInCubeData} setUpdateList={setUpdateList} setSelectedID={setSelectedID} loader={loader} setLoader={setLoader} />
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={2} lg={2}>
                            <Card className="card">
                                <Record isRecord={isRecord} setIsRecord={setIsRecord} disable={disable} inCubeData={inCubeData} runData={runData} />
                            </Card>
                        </Grid>
                    </Grid>
                    <Grid container spacing={3}>
                        <Grid item xs={12} md={5} lg={4}>
                            <Card className="card">
                                <LiveImage showImage={showImage} setShowImage={setShowImage} inCubeData={inCubeData} />
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={7} lg={8}>
                            <Card className="card">
                                <AtmosphereSlider inCubeData={inCubeData} setInCubeData={setInCubeData} newupdate={newupdate} setDisable={setDisable} runData={runData} setRunData={setRunData} setSelectedID={setSelectedID} delayEdit={delayEdit} setDelayEdit={setDelayEdit} showAtmPen={showAtmPen} setAtmShowPen={setAtmShowPen} />
                            </Card>
                        </Grid>
                    </Grid>
                    <Grid container spacing={3}>
                        <Grid item xs={12} md={12} lg={12}>
                            <Card className="card">
                                <LightLevelSlider inCubeData={inCubeData} setInCubeData={setInCubeData} setDisable={setDisable} runData={runData} setRunData={setRunData} setSelectedID={setSelectedID} delayEdit={delayEdit} setDelayEdit={setDelayEdit} showLightPen={showLightPen} setLightShowPen={setLightShowPen} />
                            </Card>
                        </Grid>
                    </Grid>
                    <Grid container spacing={3}>
                        <Grid item xs={12} md={12} lg={12}>
                            <Card className="card">
                                {/* <Graph inCubeData={inCubeData} setInCubeData={setInCubeData} setRunData={setRunData} runData={runData}/> */}
                                <Graph inCubeData={inCubeData} setInCubeData={setInCubeData} setRunData={setRunData} runData={runData} setDelayEdit={setDelayEdit} showGraphIcon={showGraphIcon} setShowGraphIcon={setShowGraphIcon} />
                            </Card>
                        </Grid>
                    </Grid>
                </Container>
            </main>
            <div>
                <SideBar setSelectedID={setSelectedID} selectedID={selectedID} updateList={updateList} deleteBtn={deleteBtn} setDeleteBtn={setDeleteBtn} setShowImage={setShowImage} />
            </div>
            <Spinner loader={loader} setLoader={setLoader}/>
        </div>
    );
}