import React, { useEffect, useState } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import { useFormik } from 'formik';
import CreateIcon from '@material-ui/icons/Create';
import { makeStyles } from '@material-ui/core/styles';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import DescriptionIcon from '@material-ui/icons/Description';
import Tooltip from '@material-ui/core/Tooltip';
import baseUrl from "../../config/config";
import '../../styles/cropDetailModel.css';

const useStylesBootstrap = makeStyles((theme) => ({
    arrow: {
      color: theme.palette.common.black,
    },
    tooltip: {
      backgroundColor: theme.palette.common.black,
      fontSize: '15px',
      marginTop: '8px !important',
    },
}));

function BootstrapTooltip(props) {
    const classes = useStylesBootstrap();
    return <Tooltip arrow classes={classes} className="text" {...props} />;
}

export default function CropDetailModal(props) {

    const [open, setOpen] = React.useState(false);

    const [scroll, setScroll] = React.useState('paper');

    const descriptionElementRef = React.useRef(null);

    const[id, setId] = useState();

    const[name, setName] = useState();

    const[edit, setEdit] = React.useState(true);

    useEffect(()=>{
        if(props && props.cropDetails){
            setId(props.cropDetails.incubator_id);
            setName(props.cropDetails.name);
            formik.setFieldValue("crop_name", props.cropDetails.name)
        }
    },[props])

    const handleClickOpen = (scrollType) => () => {
        if(props.inCubeData){
            props.cb();
            setOpen(true);
            setScroll(scrollType);
        }
    };

    const handleClose = () => {
        setOpen(false);
    };

    useEffect(() =>{
        setOpen(props.open)
    },[props.open])

    React.useEffect(() => {
        if (open) {
            const { current: descriptionElement } = descriptionElementRef;
            if (descriptionElement !== null) {
                descriptionElement.focus();
            }
        }
    }, [open]);

    function temp(input) {
        if (input.target.value < -40) input.target.value = -40;
        if (input.target.value > 158) input.target.value = 158;
        formik.setFieldValue("temp", input.target.value)
    }

    function co2(input) {
        if (input.target.value < 0) input.target.value = 0;
        if (input.target.value > 100) input.target.value = 100;
        formik.setFieldValue("co2", input.target.value);
    }

    function humidity(input) {
        if (input.target.value < 0) input.target.value = 0;
        if (input.target.value > 100) input.target.value = 100;
        formik.setFieldValue("humidity", input.target.value);
    }

    function electrical_conductivity(input) {
        if (input.target.value < 0) input.target.value = 0;
        if (input.target.value > 10) input.target.value = 10;
        formik.setFieldValue("electrical_conductivity", input.target.value);
    }

    function pH(input) {
        if (input.target.value < 0) input.target.value = 0;
        if (input.target.value > 14) input.target.value = 14;
        formik.setFieldValue("pH", input.target.value);
    }

    function deep_red(input) {
        if (input.target.value < 0) input.target.value = 0;
        if (input.target.value > 100) input.target.value = 100;
        formik.setFieldValue("deep_red", input.target.value);
    }

    function red(input) {
        if (input.target.value < 0) input.target.value = 0;
        if (input.target.value > 100) input.target.value = 100;
        formik.setFieldValue("red", input.target.value);
    }

    function white(input) {
        if (input.target.value < 0) input.target.value = 0;
        if (input.target.value > 100) input.target.value = 100;
        formik.setFieldValue("white", input.target.value);
    }

    function blue(input) {
        if (input.target.value < 0) input.target.value = 0;
        if (input.target.value > 100) input.target.value = 100;
        formik.setFieldValue("blue", input.target.value);
    }

    function deep_blue(input) {
        if (input.target.value < 0) input.target.value = 0;
        if (input.target.value > 100) input.target.value = 100;
        formik.setFieldValue("deep_blue", input.target.value);
    }

    function uv(input) {
        if (input.target.value < 0) input.target.value = 0;
        if (input.target.value > 100) input.target.value = 100;
        formik.setFieldValue("uv", input.target.value)
    }

    const initialValues = {
        crop_name: '',
        temp: '',
        co2: '',
        humidity: '',
        electrical_conductivity: '',
        pH: '',
        deep_red: '',
        red: '',
        white: '',
        blue: '',
        deep_blue: '',
        uv: '',
        start_date: '',
        end_date: ''
    }

    useEffect(()=>{
       if(props.cropDetails){
           formik.setFieldValue("temp", props.cropDetails.temperature);
           formik.setFieldValue("co2", props.cropDetails.co2);
           formik.setFieldValue("humidity", props.cropDetails.humidity);
           formik.setFieldValue("electrical_conductivity", props.cropDetails.electrical_conductivity);
           formik.setFieldValue("pH", props.cropDetails.pH);
           formik.setFieldValue("deep_red", props.cropDetails.deep_red);
           formik.setFieldValue("red", props.cropDetails.red);
           formik.setFieldValue("white", props.cropDetails.white);
           formik.setFieldValue("blue", props.cropDetails.blue);
           formik.setFieldValue("deep_blue", props.cropDetails.deep_blue);
           formik.setFieldValue("uv", props.cropDetails.uv);
           formik.setFieldValue("start_date", props.cropDetails.start_date);
           formik.setFieldValue("end_date", props.cropDetails.end_date);
       }
    },[props.cropDetails])

    function updateFormMode(e) {
        e.preventDefault();
        setEdit(!edit);
    }

    const onSubmit = (values) => {
        var token = localStorage.getItem("token");
        var selectedId = localStorage.getItem("selectedId");
        var postCropData ={
            atmosphere:{
                incubator_id: selectedId,
                name: values.crop_name,
                temp: values.temp,
                co2: values.co2,
                humidity: values.humidity,
                electrical_conductivity: values.electrical_conductivity,
                pH: values.pH,
                start_date: values.start_date,
                end_date: values.end_date,
            },
            light:{
                deep_red: values.deep_red,
                red: values.red,
                white: values.white,
                blue: values.blue,
                deep_blue: values.deep_blue,
                uv: values.uv,
            },
        };
        fetch(`${baseUrl.baseUrl}addincubatordata/`, {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json",
              Authorization: token,
            },
            body: JSON.stringify(postCropData),
          })
        .then((res) => res.json())
        .then((results) => {
            setEdit(!edit)
        });
    }

    const validate = (values) => {
       
        let errors = {}

        if(!values.crop_name) {  
            errors.crop_name = 'Please enter crop name.'
        }
        if(!values.temp) {
            errors.temp = 'Please enter temperature value.'
        } 
        if(!values.co2) {
            errors.co2 = 'Please enter CO value.'
        }
        if(!values.humidity) {
            errors.humidity = 'Please enter humidity value.'
        }
        if(!values.electrical_conductivity) {
            errors.electrical_conductivity = 'Please enter electrolyte value.'
        }
        if(!values.pH) {
            errors.pH = 'Please enter pH value.'
        }
        if(!values.deep_red) {
            errors.deep_red = 'Please enter deep red value.'
        }
        if(!values.red) {
            errors.red = 'Please enter red value.'
        }
        if(!values.white) {
            errors.white = 'Please enter white value.'
        }
        if(!values.blue) {
            errors.blue = 'Please enter blue value.'
        }
        if(!values.deep_blue) {
            errors.deep_blue = 'Please enter deep blue value.'
        }
        if(!values.uv) {
            errors.uv = 'Please enter uv value.'
        }
        if(!values.start_date) {
            errors.start_date = 'Please enter start date.'
        }
        if(!values.end_date) {
            errors.end_date = 'Please enter end date.'
        }
        return errors;  
    }

    const formik = useFormik({
        initialValues,
        onSubmit,
        validate
    })

    return (
        <div>
            <BootstrapTooltip classname="text" title="Preset Program">
                <div className={props.inCubeData? " " : "IscropdetailBtn"}>
                    <div className={props.inCubeData? "crop_detail" : " crop_detail IscropdetailBtn"} onClick={handleClickOpen()}>
                        <p className={props.inCubeData?"crop_detail_title text" : "text IscropdetailBtn"}>Crop Detail</p>
                        <DescriptionIcon className={props.inCubeData?"description_icon ":"description_icon  IscropdetailBtn"} />
                    </div>
                </div>
            </BootstrapTooltip>
            <Dialog
                open={open}
                scroll={scroll}
                className="crop_detail_model_width"
            >
                 <div className="crop_detail_title_heading">
                    <DialogTitle id="scroll-dialog-title">
                        <Grid container spacing={2}>
                            <Grid item xs={12} sm={2} md={2}>
                                <div className="back_text_center">
                                    <div className="back_icon" onClick={handleClose}>
                                        <ArrowBackIcon className="go_back_icon" />
                                    </div>
                                </div>
                            </Grid>
                            <Grid item xs={12} sm={8} md={8}>
                                <div className="crop_title">
                                    <span className="text">Kindly Fill The Crop Detail's Here</span>
                                </div>
                            </Grid>
                            <Grid item xs={12} sm={2} md={2}>
                                {props.showEdit ? (
                                <div className="edit_text_center">
                                    <div className="edit_icon">
                                        <CreateIcon className="go_edit_icon" 
                                         onClick={(e) => updateFormMode(e)}
                                         color={edit ? "primary" : ""}      
                                        />
                                    </div>
                                </div>
                                ): null}
                            </Grid>
                        </Grid>
                    </DialogTitle>
                </div>
                <DialogContent dividers={scroll === 'paper'}>
                    <form onSubmit={formik.handleSubmit}>
                        <DialogContentText className="text">
                            <Grid container spacing={2}>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Incubator Id*"
                                        type="text"
                                        name="incubator_id"
                                        id="incubator_id"
                                        value={"Incubator Id No. - " + id}
                                        InputProps={{
                                            readOnly: true,
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter Crop Name*"
                                        type="text"
                                        name="crop_name"
                                        id="crop_name"
                                        autoComplete="off"
                                        disabled={edit}
                                        onChange={formik.handleChange}
                                        onBlur={formik.handleBlur}
                                        value={formik.values.crop_name}
                                    />
                                    {formik.touched.crop_name && formik.errors.crop_name ? <div className="error">{formik.errors.crop_name}</div> : null}
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="temp_label">
                                        Temperature (-40 to 158F)*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter Temperature Value*"
                                        type="number"
                                        name="temp"
                                        autoComplete="off"
                                        disabled={edit}
                                        value={formik.values.temp}
                                        onChange={(e)=>{
                                            formik.handleChange(e);
                                            temp(e)
                                        }}
                                        onBlur={formik.handleBlur}
                                        onKeyDown={ e => ( e.keyCode === 69 || e.keyCode === 190 ) && e.preventDefault() }
                                    />
                                    {formik.touched.temp && formik.errors.temp ? <div className="error">{formik.errors.temp}</div> : null}
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="co2_label">
                                        CO (0 to 100%)*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter CO Value*"
                                        type="number"
                                        name="co2"
                                        id="min_co2"
                                        autoComplete="off"
                                        disabled={edit}
                                        value={formik.values.co2}
                                        onChange={(e)=>{
                                            formik.handleChange(e);
                                            co2(e)
                                        }}
                                        onBlur={formik.handleBlur}
                                        onKeyDown={ e => ( e.keyCode === 69 || e.keyCode === 190 ) && e.preventDefault() }
                                    />
                                    {formik.touched.co2 && formik.errors.co2 ? <div className="error">{formik.errors.co2}</div> : null}
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="humidity_label">
                                        Humidity (0 to 100%)*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter Humidity value*"
                                        type="number"
                                        name="humidity"
                                        id="min_humidity"
                                        autoComplete="off"
                                        disabled={edit}
                                        value={formik.values.humidity}
                                        onChange={(e)=>{
                                            formik.handleChange(e);
                                            humidity(e)
                                        }}
                                        onBlur={formik.handleBlur}
                                        onKeyDown={ e => ( e.keyCode === 69 || e.keyCode === 190 ) && e.preventDefault() }
                                    />
                                    {formik.touched.humidity && formik.errors.humidity ? <div className="error">{formik.errors.humidity}</div> : null}
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="electrolyte_label">
                                        Electrolyte (0 to 10)*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter Electrolyte Value*"
                                        type="number"
                                        name="electrical_conductivity"
                                        id="min_electrical_conductivity"
                                        autoComplete="off"
                                        disabled={edit}
                                        value={formik.values.electrical_conductivity}
                                        onChange={(e)=>{
                                            formik.handleChange(e);
                                            electrical_conductivity(e)
                                        }}
                                        onBlur={formik.handleBlur}
                                        onKeyDown={ e => ( e.keyCode === 69 || e.keyCode === 190 ) && e.preventDefault() }
                                    />
                                    {formik.touched.electrical_conductivity && formik.errors.electrical_conductivity ? <div className="error">{formik.errors.electrical_conductivity}</div> : null}
                                </Grid>
                            </Grid>
                            {/* <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="electrolyte_label">
                                        pH (0 to 14)*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter pH Value*"
                                        type="text"
                                        name="pH"
                                        id="min_pH"
                                        autoComplete="off"
                                        disabled={edit}
                                        value={formik.values.pH}
                                        onChange={(e)=>{
                                            formik.handleChange(e);
                                            pH(e)
                                        }}
                                        onBlur={formik.handleBlur}
                                    />
                                    {formik.touched.pH && formik.errors.pH ? <div className="error">{formik.errors.pH}</div> : null}
                                </Grid>
                            </Grid> */}
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="deep_red_label">
                                        Deep Red (0 to 100%)*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter Deep Red Value*"
                                        type="number"
                                        name="deep_red"
                                        id="deep_red"
                                        autoComplete="off"
                                        disabled={edit}
                                        value={formik.values.deep_red}
                                        onChange={(e)=>{
                                            formik.handleChange(e);
                                            deep_red(e)
                                        }}
                                        onBlur={formik.handleBlur}
                                        onKeyDown={ e => ( e.keyCode === 69 || e.keyCode === 190 ) && e.preventDefault() }
                                    />
                                    {formik.touched.deep_red && formik.errors.deep_red ? <div className="error">{formik.errors.deep_red}</div> : null}
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="deep_red_label">
                                        Red (0 to 100%)*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter Red Value*"
                                        type="number"
                                        name="red"
                                        id="red"
                                        autoComplete="off"
                                        disabled={edit}
                                        value={formik.values.red}
                                        onChange={(e)=>{
                                            formik.handleChange(e);
                                            red(e)
                                        }}
                                        onBlur={formik.handleBlur}
                                        onKeyDown={ e => ( e.keyCode === 69 || e.keyCode === 190 ) && e.preventDefault() }
                                    />
                                    {formik.touched.red && formik.errors.red ? <div className="error">{formik.errors.red}</div> : null}
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="deep_red_label">
                                        White (0 to 100%)*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter White Value*"
                                        type="number"
                                        name="white"
                                        id="white"
                                        autoComplete="off"
                                        disabled={edit}
                                        value={formik.values.white}
                                        onChange={(e)=>{
                                            formik.handleChange(e);
                                            white(e)
                                        }}
                                        onBlur={formik.handleBlur}
                                        onKeyDown={ e => ( e.keyCode === 69 || e.keyCode === 190 ) && e.preventDefault() }
                                    />
                                    {formik.touched.white && formik.errors.white ? <div className="error">{formik.errors.white}</div> : null}
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="deep_red_label">
                                        Blue (0 to 100%)*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter Blue Value*"
                                        type="number"
                                        name="blue"
                                        id="blue"
                                        autoComplete="off"
                                        disabled={edit}
                                        value={formik.values.blue}
                                        onChange={(e)=>{
                                            formik.handleChange(e);
                                            blue(e)
                                        }}
                                        onBlur={formik.handleBlur}
                                        onKeyDown={ e => ( e.keyCode === 69 || e.keyCode === 190 ) && e.preventDefault() }
                                    />
                                    {formik.touched.blue && formik.errors.blue ? <div className="error">{formik.errors.blue}</div> : null}
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="deep_red_label">
                                        Deep Blue (0 to 100%)*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter Deep Blue Value*"
                                        type="number"
                                        name="deep_blue"
                                        id="deep_blue"
                                        autoComplete="off"
                                        disabled={edit}
                                        value={formik.values.deep_blue}
                                        onChange={(e)=>{
                                            formik.handleChange(e);
                                            deep_blue(e)
                                        }}
                                        onBlur={formik.handleBlur}
                                        onKeyDown={ e => ( e.keyCode === 69 || e.keyCode === 190 ) && e.preventDefault() }
                                    />
                                    {formik.touched.deep_blue && formik.errors.deep_blue ? <div className="error">{formik.errors.deep_blue}</div> : null}
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="deep_red_label">
                                        UV (0 to 100%)*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        label="Please Enter UV Value*"
                                        type="number"
                                        name="uv"
                                        id="uv"
                                        autoComplete="off"
                                        disabled={edit}
                                        value={formik.values.uv}
                                        onChange={(e)=>{
                                            formik.handleChange(e);
                                            uv(e)
                                        }}
                                        onBlur={formik.handleBlur}
                                        onKeyDown={ e => ( e.keyCode === 69 || e.keyCode === 190 ) && e.preventDefault() }
                                    />
                                    {formik.touched.uv && formik.errors.uv ? <div className="error">{formik.errors.uv}</div> : null}
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="start_date_label">
                                        Start Date*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        type="date"
                                        name="start_date"
                                        id="start_date"
                                        autoComplete="off"
                                        disabled={edit}
                                        onChange={formik.handleChange}
                                        onBlur={formik.handleBlur}
                                        value={formik.values.start_date}
                                    />
                                    {formik.touched.start_date && formik.errors.start_date ? <div className="error">{formik.errors.start_date}</div> : null}
                                </Grid>
                            </Grid>
                            <Grid container spacing={3} className="input_field">
                                <Grid item xs={12} sm={6} md={6}>
                                    <div className="end_date_label">
                                        End Date*
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6}>
                                    <TextField
                                        margin="normal"
                                        fullWidth
                                        type="date"
                                        name="end_date"
                                        id="end_date"
                                        autoComplete="off"
                                        disabled={edit}
                                        onChange={formik.handleChange}
                                        onBlur={formik.handleBlur}
                                        value={formik.values.end_date}
                                    />
                                    {formik.touched.end_date && formik.errors.end_date ? <div className="error">{formik.errors.end_date}</div> : null}
                                </Grid>
                            </Grid>
                        </DialogContentText>
                        {!edit && 
                        <DialogActions className="text_left">
                            <div>
                                <Button type="submit" variant="contained" className="btn_color" onClick={handleClose}>
                                    <span className="text text_color">Submit</span>
                                </Button>
                            </div>
                        </DialogActions>
                        }
                    </form>
                </DialogContent>
            </Dialog>
        </div>
    );
}
