import React, { useEffect, useState } from 'react';
import DeleteIcon from '@material-ui/icons/Delete';
import Tooltip from '@material-ui/core/Tooltip';
import { makeStyles } from '@material-ui/core/styles';
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import CheckOutlinedIcon from "@material-ui/icons/CheckOutlined";
import ClearOutlinedIcon from "@material-ui/icons/ClearOutlined";
import baseUrl from '../../config/config';
import '../../styles/deleteIncubator.css';

const useStylesBootstrap = makeStyles((theme) => ({
    arrow: {
      color: theme.palette.common.black,
    },
    tooltip: {
      backgroundColor: theme.palette.common.black,
      fontSize: '15px',
      marginTop: '8px !important',
    },
}));

function BootstrapTooltip(props) {
    const classes = useStylesBootstrap();
    return <Tooltip arrow classes={classes} className="text" {...props} />;
}

export default function DeleteIncubator(props) {

    const [deleteErrMsg, setDeleteErrMsg] = useState('');

    const [open, setOpen] = React.useState(false);

    const handleClickOpen = (scrollType) => () => {
        if(props.inCubeData){
            setOpen(true);
        }
    };

    useEffect(()=>{
        setOpen(props.open)
    },[props.open])

    const handleClose = () => {
        setOpen(false);
    };

    function deleteIncubatorList() {
        props.setLoader(true);
        var token = localStorage.getItem('token')
        var incubator_id = localStorage.getItem('selectedId')
        var postDeleteData = { "incubator_id" : incubator_id }
        fetch(`${baseUrl.baseUrl}delete_incubator/`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                Authorization: token
            },
            body: JSON.stringify(postDeleteData)
        })
        .then((res) => res.json())
        .then((results) => {
            if (results.msg) {
                props.setLoader(false);
                props.setUpdateList('')
                props.setUpdateList(incubator_id)
                props.setInCubeData('')   
                props.setInCubeData(true)
                localStorage.removeItem('selectedId')
                props.setInCubeData('')
                setOpen(false)
                window.location.reload()
            } else {
                setDeleteErrMsg(results.error)
            }
        });
    }

    return (
        <div>
            <BootstrapTooltip className="text" title="Delete Incubator/Tray">
                <div className={props.inCubeData? " " : "IsdeleteBtn"}>
                    <div className={props.inCubeData? "delete_title" : " delete_title IsdeleteBtn"} onClick={handleClickOpen()}>
                        <p className={props.inCubeData?"delete_line text" : "text IsdeleteBtn"}>Delete</p>
                        <DeleteIcon className={props.inCubeData?"delete_icon ":"delete_icon  IsdeleteBtn"} />
                    </div>
                </div>
            </BootstrapTooltip>
            <Dialog
                open={open}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    <p className="text title_color">Do you want to delete this Incubator/Tray ?</p>
                </DialogTitle>
                <div className="delete_api_error">
                    {/* <span className="text error">{deleteErrMsg}</span> */}
                </div>
                <DialogActions className="button_height">
                    <span className="yes_button" onClick={()=> {deleteIncubatorList()}}>
                        <CheckOutlinedIcon />
                    </span>
                    <span className="no_button" onClick={handleClose}>
                        <ClearOutlinedIcon />
                    </span>
                </DialogActions>
            </Dialog>
        </div>
    );
}