import React, { useEffect, useState } from 'react';
import { createStyles, makeStyles, withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import EditIcon from '@material-ui/icons/Edit';
import Tooltip from '@material-ui/core/Tooltip';
import { Slider } from '@material-ui/core';
import baseUrl from '../../config/config';
import '../../styles/lightLevelSlider.css';

const useStyles = makeStyles((theme) =>

    createStyles({
        vertical: {
            height: 240,
        },
        horizontal: {
            width: 200,
            margin: '0 auto',
        },
    }),
);

const CustomSlider = withStyles({
    root: {
        height: 8,
        '&$vertical': {
            width: 8,
            marginTop: 4,
        }
    },
    thumb: {
        height: '9px !important',
        width: '90px !important',
        borderRadius: 0,
        backgroundColor: '#fff',
        border: '2px solid rgb(207, 201, 201)',
        marginTop: '-8px !important',
        marginLeft: '0px !important',
        marginBottom: '-5px !important',
        '&:focus, &:hover': {
            boxShadow: 'none',
        },
        '&$active': {
            boxShadow: 'none',
        },
        '& .bar': {
            height: 1,
            width: 85,
            backgroundColor: 'currentColor',
            marginLeft: 1,
            marginRight: 1,
        },
    },
    active: {},
    valueLabel: {
        left: 'calc(-50% + 4px)',
    },
    track: {
        height: 100,
        borderRadius: 4,
    },
    rail: {
        height: 100,
        borderRadius: 4,
    },
    vertical: {
        '& $rail': {
            width: 90,
        },
        '& $track': {
            width: 90,
        },
        '& $thumb': {
            marginLeft: -0,
            marginBottom: -6,
        }
    }
})(Slider);

function CustomThumbComponent(props) {
    return (
        <span {...props}>
            <hr className="bar" />
        </span>
    );
}

var prevCheckedArr;

const useStylesBootstrap = makeStyles((theme) => ({
    arrow: {
      color: theme.palette.common.black,
    },
    tooltip: {
      backgroundColor: theme.palette.common.black,
      fontSize: '15px',
      marginTop: '8px !important',
      marginLeft: '-50px !important',
    },
}))

function BootstrapTooltip(props) {
    const classes = useStylesBootstrap();
    return <Tooltip arrow classes={classes} className="text" {...props} />;
}

export default function LightLevelSlider(props) {

    const classes = useStyles();

    const [state, setState] = React.useState({
        checkedC: false,
        checkedD: false,
        checkedE: false,
    });

    const [checkedLmanual, setCheckedLmanual] = useState(null);
    const [checkedLauto, setCheckedLauto] = useState(null);
    const [checkedLautoamb, setCheckedLautoamb] = useState(null);
    const [manualLToggle, setManualLToggle] = useState();
    const [autoAmbidentLToggle, setAutoAmbidentLToggle] = useState();

    const [deepRed, setdeepRed] = useState(0);
    const [red, setRed] = useState(0);
    const [white, setWhite] = useState(0);
    const [blue, setBlue] = useState(0);
    const [deepBlue, setDeepBlue] = useState(0);
    const [uv, setUV] = useState(0);
    const [isDisabled, setIsDisabled] = useState(false);

    const [lightManualData , setLightManualData] = useState();
    const [lightAutoData, setLightAutoData] = useState();
    const [lightAutoAmbData, setLightAutoAmbData] = useState();
    const [isActive, setIsActive] = useState(false);

    useEffect(()=>{
        if(props.inCubeData===undefined || props.inCubeData===null){
            setdeepRed(0);
            setRed(0);
            setWhite(0);
            setBlue(0);
            setDeepBlue(0);
            setUV(0);
        }else {

        if(props.inCubeData && props.inCubeData){
            setdeepRed(props.inCubeData.deep_red)
        }

        if(props.inCubeData && props.inCubeData){
            setRed(props.inCubeData.red)
        }

        if(props.inCubeData && props.inCubeData){
            setWhite(props.inCubeData.white)
        }

        if(props.inCubeData && props.inCubeData){
            setBlue(props.inCubeData.blue)
        }

        if(props.inCubeData && props.inCubeData){
            setDeepBlue(props.inCubeData.deep_blue)
        }

        if(props.inCubeData && props.inCubeData){
            setUV(props.inCubeData.uv)
        }
    
        if(props.inCubeData && props.inCubeData.light_status !== true && props.inCubeData.light_status !== null){
            setCheckedLmanual(true)
        }

        if(props.inCubeData && props.inCubeData.light_status === null){
            setCheckedLautoamb(true)
        }
        else{
            setCheckedLauto(props.inCubeData.light_status);
        }
    }
    },[props.inCubeData])


    useEffect(()=>{
        var obj = { name: "manual", value: true };
          prevCheckedArr = obj;
    },[])

    useEffect(()=>{
        if(isActive){
            setIsDisabled(false);
            props.setDelayEdit(true)
            props.setLightShowPen(true)
        }
        else{
            setIsDisabled(true);
        }
    },[isActive])

    useEffect(()=>{
        setIsActive(props.showLightPen)
    },[props.showLightPen]);

    useEffect(() => {
        if(checkedLmanual !== null && checkedLmanual) {
            if(checkedLmanual===true){    
                fetchLightManual();
            }
          setCheckedLauto(false);
          setCheckedLautoamb(!checkedLmanual);
        }else if(
            checkedLmanual !== null && 
            !checkedLmanual && 
            !checkedLautoamb
        ) {
          setCheckedLauto(true);
        }
    }, [checkedLmanual]);
    
    useEffect(() => {
        if(checkedLautoamb !== null && checkedLautoamb) {
            if(checkedLautoamb===true){
                fetchLightautoambient();
            }
          setCheckedLmanual(!checkedLautoamb);
          setCheckedLauto(!checkedLautoamb);
        } else if(
          checkedLautoamb !== null &&
          !checkedLautoamb &&
          !checkedLmanual
        ) {
          setCheckedLauto(true);
        }
    }, [checkedLautoamb]);

    useEffect(() => {
        if(checkedLauto === null) 
            return;
            if(checkedLauto===true){
                fetchLightAuto();
                setIsActive(false);
                // props.setDisable(true);
            }
        if(checkedLautoamb) {
          var obj = { name: "autoAmb", value: checkedLautoamb };
          prevCheckedArr = obj;
        }else if(checkedLmanual) {
          var obj = { name: "manual", value: checkedLmanual };
          prevCheckedArr = obj;
        }
       
        if(!prevCheckedArr) 
            return;

        if(prevCheckedArr.name === "autoAmb") {
          setCheckedLautoamb(!checkedLauto);
        }else if(prevCheckedArr.name === "manual") {
          setCheckedLmanual(!checkedLauto);
        }
    }, [checkedLauto]);

    useEffect(()=>{
        var togglebarlmanual = localStorage.getItem('manualLToggle')
        if(togglebarlmanual==='true'){
            setCheckedLmanual(true)
        } else {
            setCheckedLmanual(false)
        }
    },[])

    useEffect(()=>{
        var togglebarlmanual = localStorage.setItem('manualLToggle', checkedLmanual)
    },[checkedLmanual])

    useEffect(()=>{
        var togglebarautoamb = localStorage.getItem('autoAmbidentLToggle')
        if(togglebarautoamb==='true'){
            setCheckedLautoamb(true)
        } else {
            setCheckedLautoamb(false)
        }
    },[])

    useEffect(()=>{
        var togglebarautoamb = localStorage.setItem('autoAmbidentLToggle', checkedLautoamb)
    },[checkedLautoamb])

    useEffect(()=>{
        if(lightManualData){
            setdeepRed(lightManualData.deep_red);
            setRed(lightManualData.red)
            setWhite(lightManualData.white);
            setBlue(lightManualData.blue);
            setDeepBlue(lightManualData.deep_blue);
            setUV(lightManualData.uv);
        }
    },[lightManualData])
 
     useEffect(()=>{
        if (lightAutoData === undefined || (lightAutoData && lightAutoData.error)) {
            setdeepRed(0);
            setRed(0);
            setWhite(0);
            setBlue(0);
            setDeepBlue(0);
            setUV(0);
        } else {
            setdeepRed(lightAutoData.deep_red);
            setRed(lightAutoData.red);
            setWhite(lightAutoData.white);
            setBlue(lightAutoData.blue);
            setDeepBlue(lightAutoData.deep_blue);
            setUV(lightAutoData.uv);
        }
    },[lightAutoData])
 
    useEffect(()=>{
        if(lightAutoAmbData){
            setdeepRed(0);
            setRed(0);
            setWhite(0);
            setBlue(0);
            setDeepBlue(0);
            setUV(0);
        }
    },[lightAutoAmbData])

    function onChangeDeepRed(e, v) {
        setdeepRed(v);
    }

    function onChangeRed(e, v) {
        setRed(v);
    }

    function onChangeWhite(e, v) {
        setWhite(v);
    }

    function onChangeBlue(e, v) {
        setBlue(v);
    }

    function onChangeDeepBlue(e, v) {
        setDeepBlue(v);
    }

    function onChangeUV(e, v) {
        setUV(v);
    }

    // code for run functionality

    function postLightData(key, value) {
        if(key === "blue"){
            props.setInCubeData({...props.inCubeData, blue: blue});
        }
        if(key === "red"){
            props.setInCubeData({...props.inCubeData, red: red});
        }
        if(key === "white"){
            props.setInCubeData({...props.inCubeData, white: white});
        }
        if(key === "deepBlue"){
            props.setInCubeData({...props.inCubeData, deep_blue: deepBlue})
        }
        if(key === "deepRed"){
            props.setInCubeData({...props.inCubeData, deep_red: deepRed});
        }
        if(key === "uv"){
            props.setInCubeData({...props.inCubeData, uv: uv});
        }
    }

    function fetchLightAuto(){
        var token = localStorage.getItem('token')
        var selectedId = localStorage.getItem('selectedId')
        var postManualData = {
            incubator_id: selectedId
        }
        fetch(`${baseUrl.baseUrl}lightauto/`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: token,
            },
            body: JSON.stringify(postManualData)
        })
        .then((res) => res.json())
        .then((results) => {
            setLightAutoData(results);
        });
    } 

    function fetchLightManual(){
        var token = localStorage.getItem('token')
        var selectedId = localStorage.getItem('selectedId')
        var postManualData = {
            incubator_id: selectedId
        }
        fetch(`${baseUrl.baseUrl}lightmanual/`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: token,
            },
            body: JSON.stringify(postManualData)
        })
        .then((res) => res.json())
        .then((results) => {
            if(results.error){
                setCheckedLmanual(false)
                // setCheckedLauto(!checkedLmanual)
            }
            setLightManualData(results);
            if(selectedId){
                props.setSelectedID('') 
                props.setSelectedID(selectedId) 
            }
        });
    } 

    function fetchLightautoambient(){
        var token = localStorage.getItem('token')
        var selectedId = localStorage.getItem('selectedId')
        var postManualData = {
            incubator_id: selectedId
        }
        fetch(`${baseUrl.baseUrl}lightautoambient/`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: token,
            },
            body: JSON.stringify(postManualData)
        })
        .then((res) => res.json())
        .then((results) => {
            setLightAutoAmbData(results);
        });
    }

    const onChangeCommitted = (event, key)=>{
        postLightData(key, event.target.ariaValueNow);
    }; 

    return (
        <div>
            <Grid container>
                <div className="lightLevelSlider_content">
                    <Grid item xs={2} sm={2} md={2} lg={2}>
                        <Typography className="deep_red">
                            <span className="deep_red_title text">Deep Red</span>
                        </Typography>
                        <div className={classes.vertical}>
                            <CustomSlider
                                ThumbComponent={CustomThumbComponent}
                                orientation="vertical"
                                defaultValue={deepRed}
                                onChange={onChangeDeepRed}
                                onChangeCommitted={(e)=>{onChangeCommitted(e, 'deepRed')}}
                                valueLabelDisplay="auto"
                                className="slider_6"
                                value={deepRed}
                                disabled={isDisabled}
                            />
                        </div>
                        <div>
                            <Button className="single_button btns text" variant="contained">
                                {deepRed}%
                            </Button>
                        </div>
                    </Grid>
                    <Grid item xs={2} sm={2} md={2} lg={2}>
                        <Typography className="red">
                            <span className="red_title text">Red</span>
                        </Typography>
                        <div className={classes.vertical}>
                            <CustomSlider
                                ThumbComponent={CustomThumbComponent}
                                orientation="vertical"
                                defaultValue={red}
                                onChange={onChangeRed}
                                onChangeCommitted={(e)=>{onChangeCommitted(e, 'red')}}
                                valueLabelDisplay="auto"
                                className="slider_7"
                                value={red}
                                disabled={isDisabled}
                            />
                        </div>
                        <div>
                            <Button className="single_button btns text" variant="contained">
                                {red}%
                            </Button>
                        </div>
                    </Grid>
                    <Grid itemitem xs={2} sm={2} md={2} lg={2}>
                        <Typography className="white">
                            <span className="white_title text">White</span>
                        </Typography>
                        <div className={classes.vertical}>
                            <CustomSlider
                                ThumbComponent={CustomThumbComponent}
                                orientation="vertical"
                                defaultValue={white}
                                onChange={onChangeWhite}
                                onChangeCommitted={(e)=>{onChangeCommitted(e, 'white')}}
                                valueLabelDisplay="auto"
                                className="slider_8"
                                value={white}
                                disabled={isDisabled}
                            />
                        </div>
                        <div>
                            <Button className="single_button btns text" variant="contained">
                                {white}%
                            </Button>
                        </div>
                    </Grid>
                    <Grid item xs={2} sm={2} md={2} lg={2}>
                        <Typography className="blue">
                            <span className="blue_title text">Blue</span>
                        </Typography>
                        <div className={classes.vertical}>
                            <CustomSlider
                                ThumbComponent={CustomThumbComponent}
                                orientation="vertical"
                                defaultValue={blue}
                                onChange={onChangeBlue}
                                onChangeCommitted={(e)=>{onChangeCommitted(e, 'blue')}}
                                valueLabelDisplay="auto"
                                className="slider_9"
                                value={blue}
                                disabled={isDisabled}
                            />
                        </div>
                        <div>
                            <Button className="single_button btns text" variant="contained">
                                {blue}%
                            </Button>
                        </div>
                    </Grid>
                    <Grid item xs={2} sm={2} md={2} lg={2}>
                        <Typography className="deep_blue">
                            <span className="deep_blue_title text">Deep Blue</span>
                        </Typography>
                        <div className={classes.vertical}>
                            <CustomSlider
                                ThumbComponent={CustomThumbComponent}
                                orientation="vertical"
                                defaultValue={deepBlue}
                                onChange={onChangeDeepBlue}
                                onChangeCommitted={(e)=>{onChangeCommitted(e, 'deepBlue')}}
                                valueLabelDisplay="auto"
                                className="slider_10"
                                value={deepBlue}
                                disabled={isDisabled}
                            />
                        </div>
                        <div>
                            <Button className="single_button btns text" variant="contained">
                                {deepBlue}%
                            </Button>
                        </div>
                    </Grid>
                    <Grid item xs={2} sm={2} md={2} lg={2}>
                        <Typography className="uv">
                            <span className="uv_title text">UV</span>
                        </Typography>
                        <div className={classes.vertical}>
                            <CustomSlider
                                ThumbComponent={CustomThumbComponent}
                                orientation="vertical"
                                defaultValue={uv}
                                onChange={onChangeUV}
                                onChangeCommitted={(e)=>{onChangeCommitted(e, 'uv')}}
                                valueLabelDisplay="auto"
                                className="slider_11"
                                value={uv}
                                disabled={isDisabled}
                            />
                        </div>
                        <div>
                            <Button className="single_button btns text" variant="contained">
                                {uv}%
                            </Button>
                        </div>
                    </Grid>
                </div>
                <div className="light_level_titles text">
                    <p>LIGHT LEVELS</p>
                </div>
                <Grid container>
                    <div className="light_levels_Slider_controls">
                        <Grid item sm={2} md={2} lg={2}>
                            <div>
                                <label className="switch">
                                    <input type="checkbox" 
                                        checked={checkedLmanual}
                                        onChange={() => {
                                        setCheckedLmanual(!checkedLmanual);
                                        }}
                                        name="checkedLmanual"
                                        color="primary"
                                        inputProps={{ "aria-label": "primary checkbox" }}
                                    />
                                    <span className="sliders round" />
                                </label>
                            </div>
                        </Grid>
                        <Grid item sm={2} md={2} lg={2}>
                            <div className="manual_title_light_levels">
                                <Typography className="text"  color={checkedLmanual ? "primary" : ""}>
                                    MANUAL
                                </Typography>
                            </div>
                        </Grid>
                        <Grid item sm={2} md={2} lg={2}>
                            <div className="switch_toggle">
                                <label className="switch">
                                    <input type="checkbox" 
                                        type="checkbox"
                                        checked={checkedLauto}
                                        onChange={() => {
                                          setCheckedLauto(!checkedLauto);
                                        }}
                                        name="checkedLauto"
                                        color="primary"
                                        inputProps={{ "aria-label": "primary checkbox" }}
                                    />
                                    <span className="sliders round" />
                                </label>
                            </div>
                        </Grid>
                        <Grid item sm={2} md={2} lg={2}>
                            <div className="auto_title">
                                <Typography className="text" color={checkedLauto ? "primary" : ""}>
                                    AUTO
                                </Typography>
                            </div>
                        </Grid>
                        <Grid item sm={2} md={2} lg={2}>
                            <div className="switch_toggle_light_levels">
                                <label className="switch">
                                    <input type="checkbox" 
                                        type="checkbox"
                                        checked={checkedLautoamb}
                                        onChange={() => {
                                            setCheckedLautoamb(!checkedLautoamb);
                                        }}
                                        name="checkedLautoamb"
                                        color="primary"
                                        inputProps={{ "aria-label": "primary checkbox" }}
                                    />
                                    <span className="sliders round" />
                                </label>
                            </div>
                        </Grid>
                        <Grid item sm={2} md={2} lg={2}>
                            <div className="auto_title_light_level">
                                <Typography className="text" color={checkedLautoamb ? "primary" : ""}>
                                    AUTO-AMBIDENT
                                </Typography>
                            </div>
                        </Grid>
                        <Grid item sm={2} md={2} lg={2}>
                            {checkedLmanual === true && (
                                <div>
                                    <BootstrapTooltip classname="text" title="Edit Program">
                                        <div className="light_edit_btn_color">
                                            <div className="light_round_edit_icon">
                                                <EditIcon onClick= {() =>{setIsActive(s => !s)}} className="light_graph_edit_icon" style={{ color: isActive ? "white" : "" }} />
                                            </div>
                                        </div>
                                    </BootstrapTooltip>
                                </div>
                            )}
                        </Grid>
                    </div>
                </Grid>
            </Grid>
        </div>
    );
}