import React, {useEffect, useState} from 'react';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import InputAdornment from '@material-ui/core/InputAdornment';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import VpnKeyOutlinedIcon from '@material-ui/icons/VpnKeyOutlined';
import { useFormik } from 'formik';
import Spinner from "./Spinner";
import { useHistory } from "react-router-dom";
import baseUrl from '../config/config';
import '../styles/resetPassword.css';

const useStyles = makeStyles((theme) => ({
    root: {
        height: '100vh',
    },
    paper: {
        margin: theme.spacing(8, 4),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        marginTop: theme.spacing(15),
    },
    form: {
        width: '89%',
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(4, 0, 3),
        color: '#fff',
        backgroundColor: '#5e5e5e',
        '&:hover': {
            background: '#5e5e5e',
        },
        padding: ' 10px 16px',
        borderRadius: '1px',
    },
}));

export default function ResetPassword(props) {

    const history = useHistory();

    const classes = useStyles();

    const [errMsg, setErrMsg] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [token, setToken] = useState('');
    const [new_password, setnew_password] = useState('');
    const [old_password, setOldPassword] = useState('');
    const [confirm_password, setconfirm_password] = useState('');

    const [open, setOpen] = React.useState(false);
    const [loader, setLoader] = React.useState(false);

    const initialValues = {
        old_password:'',
        new_password: '',
        confirm_password:''
    }

    useEffect(()=>{
        if(props.history && props.history.location && props.history.location.state &&
            props.history.location.state.length > 0) {
            setEmail(props.history.location.state[0].email)
            setPassword(props.history.location.state[0].password)
            setToken(props.history.location.state[0].token)
        }
    },[props])
    
    const onSubmit = values => {
        if (values.new_password !== values.confirm_password) {
        } 
        setLoader(true); 
        fetch(`${baseUrl.baseUrl}password/reset/`, {
            method: 'POST',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Authorization': token
        },
        body: JSON.stringify({email: email, old_password: values.old_password, 
            new_password: values.new_password, new_password_confirmation: values.confirm_password})
            }).then((res)=>res.json()).then((results)=>{
            if (results.error) {
                setErrMsg(results.error)
                setLoader(false)
                setTimeout(() => { setErrMsg('') }, 5000);
            } else if(results.password_status) {
                setErrMsg(results.error)
                setLoader(false)
                setOpen(true)
                history.push('/')
            } 
        })
    }
    
    const validate = values => {
        let errors = {}

        if (!values.old_password) {
            errors.old_password = 'Please enter your current password';
        }
        if (!values.new_password) {
            errors.new_password = 'Please enter new password';
        }
        if (!values.confirm_password) {
            errors.confirm_password = 'Please enter confirm password';
        }
        if(values.confirm_password !== '' && values.new_password !== ''){
            if (values.confirm_password !== values.new_password) {  
                errors.confirm_password = 'New password and confirm password does not match';
            }
        }
        if(values.old_password !== '' && values.password !== ''){
            if (values.old_password !== password) {  
                errors.old_password = 'Current password does not match';
            }
        }
        return errors;
    }

    const formik = useFormik({
        initialValues,
        onSubmit,
        validate
    })

    return (
        <Grid container component="main" className={classes.root}>
            <CssBaseline />
            <Grid item xs={false} sm={4} md={7}>
                <div className="vertaag_logo_reset_password">
                    <img src="/assets/image/login_img.png" />
                </div>
            </Grid>
            <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
                <div className={classes.paper}>
                    <Typography component="h1" variant="h5" className="reset_title">
                        RESET YOUR PASSWORD
                    </Typography>
                    <form className={classes.form} onSubmit={formik.handleSubmit}>
                        <TextField className={classes.textFiled}
                            margin="normal"
                            fullWidth
                            label="Email Id"
                            type="text"
                            name="email"
                            id="email"
                            value={email}
                            InputProps={{
                                readOnly: true,
                            }}
                            autoComplete="off"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="start">
                                        <MailOutlineIcon />
                                    </InputAdornment>
                                )
                            }}
                        />
                        <TextField className={classes.textFiled}
                            margin="normal"
                            fullWidth
                            label="Current Password"
                            type="password"
                            name="old_password"
                            id="old_password"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.old_password}
                            autoComplete="off"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="start">
                                        <VpnKeyOutlinedIcon />
                                    </InputAdornment>
                                )
                            }}
                        />
                        {formik.touched.old_password && formik.errors.old_password ? <div className="error">{formik.errors.old_password}</div> : null}
                        <TextField
                            margin="normal"
                            fullWidth
                            label="New Password"
                            type="password"
                            name="new_password"
                            id="new_password"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.new_password}
                            autoComplete="off"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="start">
                                        <LockOutlinedIcon />
                                    </InputAdornment>
                                )
                            }}
                        />
                        {formik.touched.new_password && formik.errors.new_password ? <div className="error">{formik.errors.new_password}</div> : null}
                        <TextField
                            margin="normal"
                            fullWidth
                            label="Confirm Password"
                            type="password"
                            name="confirm_password"
                            id="confirm_password"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.confirm_password}
                            autoComplete="off"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="start">
                                        <LockOutlinedIcon />
                                    </InputAdornment>
                                )
                            }}
                        />
                        {formik.touched.confirm_password && formik.errors.confirm_password ? <div className="error">{formik.errors.confirm_password}</div> : null}
                        <div className="error">
                            <span>{errMsg}</span>
                        </div>
                        <Button
                            fullWidth
                            className={classes.submit}
                            type="submit"
                        >
                            <span classes="submit_btn">RESET PASSWORD</span>
                        </Button>
                    </form>
                </div>
            </Grid>
            <Spinner loader={loader}/>
        </Grid>
    );
}