import React, {useState} from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import { NavLink } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import InputAdornment from '@material-ui/core/InputAdornment';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import { useFormik } from 'formik';
import Spinner from "./Spinner";
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert, { AlertProps } from '@material-ui/lab/Alert';
import baseUrl from '../config/config';
import '../styles/forgotPassword.css';

const useStyles = makeStyles((theme) => ({
    root: {
        height: '100vh',
    },
    paper: {
        margin: theme.spacing(8, 4),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        marginTop: theme.spacing(15),
    },
    form: {
        width: '89%',
        marginTop: theme.spacing(4),
    },
    submit: {
        margin: theme.spacing(4, 0, 3),
        color: '#fff',
        backgroundColor: '#5e5e5e',
        '&:hover': {
            background: '#5e5e5e',
        },
        padding: ' 10px 16px',
        borderRadius: '1px',
    },
}));

export default function ForgotPassword() {

    const classes = useStyles();

    const [errMsg, setErrMsg] = useState('');
    const [email, setEmail] = useState('');

    const [open, setOpen] = React.useState(false);
    const [loader, setLoader] = React.useState(false);

    const handleClick = () => {
        console.log('loader');
    };

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpen(false);
    };
    
    const initialValues = {
        email: '',
    }
    
    const onSubmit = values => {
        setLoader(true); 
        fetch(`${baseUrl.baseUrl}password/create/`, {
            method: 'POST',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({email: values.email})
        }).then((res)=>res.json()).then((results)=>{
            if (results.status === 200) {
                setLoader(false)
                setOpen(true)
            } else {
                setErrMsg(results.error)
                setLoader(false)
                setTimeout(() => { setErrMsg('') }, 5000);
            }
        })
    }
    
    const validate = values => {
        let errors = {}
        
        if (!values.email) {
            errors.email = 'Please enter valid email id'
        } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
            errors.email = 'Invalid email id address';
        } 
        return errors;
    }

    function Alert(props) {
        return <MuiAlert elevation={10} variant="filled" {...props} />;
    }

    const formik = useFormik({
        initialValues,
        onSubmit,
        validate
    })

    return (
        <Grid container component="main" className={classes.root}>
            <CssBaseline />
            <Grid item xs={false} sm={4} md={7}>
                <div className="vertaag_log_forgot">
                    <img src="/assets/image/login_img.png" />
                </div>
            </Grid>
            <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
                <div className={classes.paper}>
                    <Typography component="h1" variant="h5" className="forgot_title">
                        FORGOT PASSWORD
                    </Typography>
                    <form className={classes.form} onSubmit={formik.handleSubmit}>
                        <TextField className={classes.textFiled}
                            margin="normal"
                            fullWidth
                            label="Email Id"
                            type="text"
                            name="email"
                            id="email"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.email}
                            autoComplete="off"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="start">
                                        <MailOutlineIcon />
                                    </InputAdornment>
                                )
                            }}
                        />
                        {formik.touched.email && formik.errors.email ? <div className="error">{formik.errors.email}</div> : null}
                        <div className="error">
                            <span>{errMsg}</span>
                        </div>
                        <Button
                            fullWidth
                            className={classes.submit}
                            type="submit"
                            onClick={handleClick}
                        >
                            <span className="text">SEND</span>
                        </Button>
                    </form>
                    <Snackbar 
                        anchorOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }} 
                        open={open} 
                        autoHideDuration={5000} 
                        onClose={handleClose}
                    >
                        <Alert className="alert_message" onClose={handleClose} severity="success">
                            Forget password request have been sent to admin account ..!
                        </Alert>
                    </Snackbar>
                    <Grid item xs>
                        <div>
                            <NavLink to="/" className="go_back_line text">
                                Go back to login
                            </NavLink>
                        </div>
                    </Grid>
                </div>
            </Grid>
            <Spinner loader={loader}/>
        </Grid>
    );
}