import React, { useEffect } from 'react';
import AlbumIcon from '@material-ui/icons/Album';
import Tooltip from '@material-ui/core/Tooltip';
import { makeStyles } from '@material-ui/core/styles';
import '../../styles/record.css';

const useStylesBootstrap = makeStyles((theme) => ({
    arrow: {
      color: theme.palette.common.black,
    },
    tooltip: {
      backgroundColor: theme.palette.common.black,
      fontSize: '15px',
      marginTop: '8px !important',
    },
}))

function BootstrapTooltip(props) {
    const classes = useStylesBootstrap();
    return <Tooltip arrow classes={classes} className="text" {...props} />;
}

export default function Record(props) {

    const [isBlue, setIsBlue] = React.useState(true);

    const [disableRecord, setDisableRecord] = React.useState(false);

    const [open, setOpen] = React.useState(false);

    useEffect(()=>{
        let rs = localStorage.getItem("record_status")
        if(rs==='True'){
            setDisableRecord(true);
        }else if(rs==='False'){
            setDisableRecord(false);
        }
    },[props.isRecord])

    const handleChange = () => {
        setIsBlue(!isBlue)
        setOpen(!open)
    }
    
    return (
        <div>
            <BootstrapTooltip classname="text" title="Record Program">
                <div className={props.inCubeData? " " : "IsRecordBtn"}>
                    {disableRecord? (
                        <div className="record_title">
                            <p className={props.inCubeData?"record_title_line1 text" : "text IsRecordBtn"}>Record</p>
                            <AlbumIcon className={props.inCubeData?"album_icon1":"album_icon1  IsRecordBtn"} onClick={() => handleChange()} />
                        </div>
                        ):(
                        <div className="record_title">
                            <p className={props.inCubeData?"record_title_line text" : "text IsRecordBtn"}>Record</p>
                            <AlbumIcon className={props.inCubeData?"album_icon":"album_icon  IsRecordBtn"} onClick={() => handleChange()} />
                        </div>
                    )}
                </div>
            </BootstrapTooltip>
        </div>
    );
}