import React from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import Login from './components/views/Login';
import ForgotPassword from './components/views/ForgotPassword';
import ResetPassword from './components/views/ResetPassword';
import Home from './components/views/common_view/Home';
import PrivateRoute from './PrivateRoute';
import PublicRoute from './PublicRoute';

export default function RouteComponent() {

    return (
        <Router>
            <Switch>
                <PublicRoute exact component={Login} path="/" />
                <PublicRoute exact component={ForgotPassword} path="/forgot_password" />
                <PrivateRoute exact component={ResetPassword} path="/reset_password" />
                <PrivateRoute exact component={Home} path="/home" />
                <PublicRoute path="*" render={() => <Redirect to="/" />} />
            </Switch>
        </Router>
    )
}