import React, {useState} from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { NavLink } from 'react-router-dom';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import InputAdornment from '@material-ui/core/InputAdornment';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import { useFormik } from 'formik';
import Spinner from "./Spinner";
import { useHistory } from "react-router-dom";
import baseUrl from '../config/config';
import '../styles/login.css';

const useStyles = makeStyles((theme) => ({
    root: {
        height: '100vh',
    },
    paper: {
        margin: theme.spacing(8, 4),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        marginTop: theme.spacing(15),
    },
    form: {
        width: '89%',
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(4, 0, 3),
        color: '#fff',
        backgroundColor: '#5e5e5e',
        '&:hover': {
            background: '#5e5e5e',
        },
        padding: '10px 16px',
        borderRadius: '1px',
    },
}));

export default function Login() {

    const history = useHistory();

    const classes = useStyles();

    const [errMsg, setErrMsg] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [loggedIn, setLoggedIn] = useState(false);

    const [loader, setLoader] = React.useState(false);
    const [open, setOpen] = React.useState(false);
    
    const initialValues = {
        email: '',
        password: ''
    }
       
    const onSubmit = values => {
        setLoader(true)
        fetch(`${baseUrl.baseUrl}login/`, {
            method: 'POST',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({email: values.email, password: values.password})
        }).then((res)=>res.json()).then((results)=>{
            if (results.error) {
                setErrMsg(results.error)
                setLoader(false)
                setTimeout(() => { setErrMsg('') }, 4000);
            } else if(results.password_status) {
                setLoader(false)
                setOpen(true)
                localStorage.setItem('token',results.token)
                history.push('./home')
            } else if(!results.password_status) {
                setLoader(false)
                setOpen(true)
                localStorage.setItem('token',results.token)
                history.push('/reset_password', [{ email: values.email, password: values.password, token: results.token }]);
            }
        })
    }
        
    const validate = values => {
        let errors = {}

        if (!values.email) {
            errors.email = 'Please enter valid email id'
        } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
            errors.email = 'Invalid email id address';
        } 
        if (!values.password) {
            errors.password = 'Please enter valid password'
        }
        return errors;
    }

    const formik = useFormik({
        initialValues,
        onSubmit,
        validate
    })

    return (
        <Grid container component="main" className={classes.root}>
            <CssBaseline />
            <Grid item xs={false} sm={4} md={7}>
                <div className="vertaag_log">
                    <img src="/assets/image/login_img.png" />
                </div>
            </Grid>
            <Grid item xs={12} sm={8} md={5} component={Paper} elevation={5} square>
                <div className={classes.paper}>
                    <Typography component="h1" variant="h5" className="heading-title">
                        LOGIN TO YOUR ACCOUNT
                    </Typography>
                    <form className={classes.form} onSubmit={formik.handleSubmit}>
                        <TextField className={classes.textFiled}
                            margin="normal"
                            fullWidth
                            label="Email Id"
                            type="text"
                            name="email"
                            id="email"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.email}
                            autoComplete="off"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="start">
                                        <MailOutlineIcon />
                                    </InputAdornment>
                                )
                            }}
                        />
                        {formik.touched.email && formik.errors.email ? <div className="error">{formik.errors.email}</div> : null}
                        <TextField
                            margin="normal"
                            fullWidth
                            label="Password"
                            type="password"
                            name="password"
                            id="password"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.password}
                            autoComplete="off"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="start">
                                        <LockOutlinedIcon />
                                    </InputAdornment>
                                )
                            }}
                        />
                        {formik.touched.password && formik.errors.password ? <div className="error">{formik.errors.password}</div> : null}
                        <Grid item xs>
                            <div className="error">
                                <span className="text">{errMsg}</span>
                                <div className="forgot-title">
                                    <NavLink to="/forgot_password" className="forgot-word-line">
                                        Forgot Password?
                                    </NavLink>
                                </div>
                            </div>
                        </Grid>
                        <FormControlLabel
                            className="remember text"
                            control={
                                <Checkbox
                                    icon={<CheckBoxOutlineBlankIcon fontSize="small" />}
                                    checkedIcon={<CheckBoxIcon fontSize="small" color="primary" />}
                                    name="checkedI"
                                />
                            }
                            label="Remember me"
                        />
                        <Button
                            fullWidth
                            className={classes.submit}
                            type="submit"
                        >
                            <span className="text">LOGIN</span>
                        </Button>
                    </form>
                </div>
            </Grid>
            <Spinner loader={loader}/>
        </Grid>
    );
}