import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import CheckOutlinedIcon from "@material-ui/icons/CheckOutlined";
import ClearOutlinedIcon from "@material-ui/icons/ClearOutlined";
import Tooltip from '@material-ui/core/Tooltip';
import BubbleChartIcon from '@material-ui/icons/BubbleChart';
import baseUrl from '../../config/config';
import '../../styles/runProgram.css';

const useStylesBootstrap = makeStyles((theme) => ({
    arrow: {
      color: theme.palette.common.black,
    },
    tooltip: {
      backgroundColor: theme.palette.common.black,
      fontSize: '15px',
      marginTop: '8px !important',
    },
}));

function BootstrapTooltip(props) {
    const classes = useStylesBootstrap();
    return <Tooltip arrow classes={classes} className="text" {...props} />;
}

export default function RunProgram(props) {

    const [open, setOpen] = React.useState(false);

    const handleClickOpen = (scrollType) => () => {
        if(props.inCubeData){
            setOpen(true);
        }
    };

    useEffect(()=>{
        setOpen(props.open)
    },[props.open])

    const handleClose = () => {
        setOpen(false);
        let rData = props.runData
        rData['record_status'] = 'False'
        runData(rData)
    };

    function onRecordClick() {
        // props.setDisable(false);
        setOpen(false);
        let rData = props.runData
        rData['record_status'] = 'True'
        runData(rData)
        props.setIsRecord(true);
    }

    // run button functionality
    
    function runData(){
        var token = localStorage.getItem('token');
        var selectedId = localStorage.getItem('selectedId');
        
        var postRunData={
            "atmosphere": {
                "incubator_id": selectedId,
                "temperature": props.inCubeData.temperature,
                "co2": props.inCubeData.co2,
                "humidity": props.inCubeData.humidity,
                "electrical_conductivity": props.inCubeData.electrical_conductivity,
                "pH": props.inCubeData.pH
            },
            "light": {
                "incubator_id": selectedId,
                "deep_red": props.inCubeData.deep_red,
                "red": props.inCubeData.red,
                "white": props.inCubeData.white,
                "blue": props.inCubeData.blue,
                "deep_blue": props.inCubeData.deep_blue,
                "uv": props.inCubeData.uv
            },
            "water":{
                "values": props.inCubeData.values
                // "water_frequency":props.inCubeData.water_frequency,
                // "water_duration":props.inCubeData.water_duration
            },
            "record_status": props.runData.record_status
            
        }

        localStorage.setItem("record_status", props.runData.record_status);
        fetch(`${baseUrl.baseUrl}manual_run/`, {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json",
              Authorization: token,
            },
            // body: JSON.stringify(props.runData),
            body: JSON.stringify(postRunData),
        })
        .then((res) => res.json())
        .then((results) => {
        props.setIsRecord(postRunData.record_status);
        props.setDelayEdit(false);
        props.setAtmShowPen(false);
        props.setLightShowPen(false);
        // props.setShowPen(false);
        });
    }

    return (
        <div>
            <BootstrapTooltip classname="text" title="Run Program">
                <div className={props.inCubeData? " " : "IsRunBtn"}>
                    <div className={props.inCubeData? "run_title" : " run_title IsRunBtn"} onClick={handleClickOpen()}>
                        <p className={props.inCubeData?"run_title_line text" : "text IsRunBtn"}>Run</p>
                        <BubbleChartIcon className={props.inCubeData?"bubbleChart_icon":"bubbleChart_icon  IsRunBtn"} />
                    </div>
                </div>
            </BootstrapTooltip>
            <Dialog
                open={open}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    <p className="text title_color">Do you want to record this data?</p>
                </DialogTitle>
                <DialogActions className="button_height">
                    <span className="yes_button" onClick={()=> {onRecordClick()}}>
                        <CheckOutlinedIcon />
                    </span>
                    <span className="no_button" onClick={handleClose}>
                        <ClearOutlinedIcon />
                    </span>
                </DialogActions>
            </Dialog>
        </div>
    );
}