import React, {useEffect, useState} from 'react';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import PowerSettingsNewIcon from '@material-ui/icons/PowerSettingsNew';
import baseUrl from '../../config/config';
import { useHistory } from 'react-router-dom';
import '../../styles/sidebar.css';

const drawerWidth = 240;

const useStyles = makeStyles((theme) =>
    createStyles({
        drawer: {
            width: drawerWidth,
            flexShrink: 0,
        },
        drawerPaper: {
            width: drawerWidth,
            backgroundColor: '#2c3742 !important',
        },
        toolbar: theme.mixins.toolbar,
        content: {
            flexGrow: 1,
            backgroundColor: theme.palette.background.default,
            padding: theme.spacing(3),
        },
    }),
);

export default function SideBar(props) {
   
    const classes = useStyles();

    const history = useHistory();

    const [open, setOpen] = React.useState(false);

    const [selectedID, setSelectedID] = useState();

    const [incList, setIncList] = useState([]);

    useEffect(() => {
        getList()
    }, []);
    
    // code for highlighting

    useEffect(()=>{
        setSelectedID(Number(props.selectedID))
    },[props.selectedID])

    useEffect(()=>{
        if(incList !== undefined && incList.length ===0){
            props.setShowImage(false);
        }
        else{
            props.setShowImage(true);
        }
        var sidbar_tab = localStorage.getItem('selectedId')
        if(!sidbar_tab){
            if(incList && incList.length>0){
            props.setSelectedID(incList[0].incubator_id)
            setSelectedID(Number(incList[0].incubator_id))
            } 
        }
    },[incList])

    useEffect(()=>{
        if(props.openModal){
            setOpen(true)
        }
    },[props.openModal])

    useEffect(() => {
        if (props.updateList) {
            getList()
            props.setSelectedID(props.updateList)
        }
    }, [props.updateList])

    function logout(){
        localStorage.clear()
        history.push('/');
    }
    
    const handleClose = () => {
        setOpen(false);
        props.setOpenModal(false)
    };

    function getList(){
        var token = localStorage.getItem('token') 
        fetch(`${baseUrl.baseUrl}incubator/`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: token,
            },
        })
        .then((res) => res.json())
        .then((results) => {
            setIncList(results.incubators_list)
        });
    }
    
    return (
        <Drawer
            className={classes.drawer}
            variant="permanent"
            classes={{
                paper: classes.drawerPaper,
            }}
            anchor="right"
        >
            <div className={classes.toolbar}>
                <div className="sidebar">
                    <img src="/assets/image/sidebar_logo.png" className="sidebar_img"/>
                </div>  
            </div>
            <Divider />
            <List className="incube_list">
                {incList&&incList.map((text, index) => (
                    <ListItem button key={text.incubator_id} selected={selectedID===text.incubator_id} onClick={()=>{props.setSelectedID(text.incubator_id)}}>
                        <ListItemText primary={"ID:" + ' ' + text.incubator_id + ' ' + text.name + ":" + ' ' + text.start_date + ' ' + "-" + ' ' + text.end_date} className="list" /> 
                    </ListItem>
                ))}
            </List>
            <div>
                <Typography variant="h6" className="sidebar_footer">
                    <PowerSettingsNewIcon className="logout_icon" />
                    <span className="logout_btn text" onClick={logout}>Logout</span>
                </Typography>
            </div>
        </Drawer>
    );
}