import React, { useRef, useEffect, useState } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Tooltip from "@material-ui/core/Tooltip";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import InsertDriveFileIcon from "@material-ui/icons/InsertDriveFile";
import Paper from "@material-ui/core/Paper";
import TextField from "@material-ui/core/TextField";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import Tabs from "@material-ui/core/Tabs";
import ClearSharpIcon from "@material-ui/icons/ClearSharp";
import Tab from "@material-ui/core/Tab";
import EventNoteIcon from "@material-ui/icons/EventNote";
import baseUrl from '../../config/config';
import '../../styles/newEntryModal.css';

const useStyles = makeStyles((theme) => ({
    root: {
        "& > *": {
        margin: theme.spacing(1),
        },
    },
    input: {
        display: "none",
    },
}));

const useStylesBootstrap = makeStyles((theme) => ({
  arrow: {
        color: theme.palette.common.black,
    },
    tooltip: {
        backgroundColor: theme.palette.common.black,
        fontSize: "15px",
        marginTop: "8px !important",
    },
}));

function BootstrapTooltip(props) {
    const classes = useStylesBootstrap();
    return <Tooltip arrow classes={classes} className="text" {...props} />;
}

export default function NewEntryModal(props) {

    const classes = useStyles();

    var Spinner = require("react-spinkit");

    const [randomNo, setRandomNo] = useState();

    const [showLoader, setShowLoader] = useState(true);

    const [open, setOpen] = React.useState(false);

    const [scroll, setScroll] = React.useState("paper");

    const descriptionElementRef = React.useRef(null);

    const [img, setImg] = useState(null);

    const [csvFile, setCsvFile] = useState(null);

    const fRef = useRef(null);

    const [csvData, setCsvData] = useState([]);

    const [showParentBtns, setShowParentBtns] = useState(true);

    const [value, setValue] = React.useState(0);

    const [errCSVMsg, setErrCSVMsg] = useState("");

    const [progress, setProgress] = React.useState(0);

    const [mac, setMac] = useState();

    const [openManual, setOpenManual] = useState(false);

    const [showScan, setShowScan] = useState(false);

    const [showCloudBtn, setShowCloudBtn] = useState(false);

    const [showTrayType, setShowTrayType] = useState(false);

    const handleChange = (event, newValue) => {
        if (newValue === 0) {
            fRef.current.click();
        } else {
            setOpenManual(true);
        }
        setValue(newValue);
    };

    const handleClick = (event) => {};

    React.useEffect(() => {
        const timer = setInterval(() => {
        setProgress((prevProgress) =>
            prevProgress >= 100 ? 0 : prevProgress + 10
            );
        }, 800);

        return () => {
            clearInterval(timer);
        };
    }, []);

    useEffect(() => {
        if (!showLoader) {
            setRandomNo(getRandomNumber());
        }
    }, [showLoader]);

    function getRandomNumber() {
        return Math.floor(Math.random() * 90000) + 10000;
    }

    var regex = new RegExp("(.*?).(csv)$");

    function onChangeHandler(e) {
        let tempCsvFile = fRef.current.files[0];
        setCsvFile(tempCsvFile);

        var isCsv = regex.test(tempCsvFile && tempCsvFile.name);
        if (isCsv) {
            setImg(tempCsvFile.name);
            setErrCSVMsg("");
        } else if (tempCsvFile) {
            setImg("");
            setErrCSVMsg("Please upload .csv file only.");
        }
    }

    function flushFile() {
        if (fRef && fRef.current) {
            fRef.current.value = "";
            setImg(null);
        }
    }

    const handleClickOpen = (scrollType) => () => {
        setOpen(true);
        setScroll(scrollType);
    };

    function onScanClick() {
        setShowScan(true);
        setShowLoader(true);
        setTimeout(() => {
        setShowLoader(false);
        }, 3000);
    }

    function handleClose() {
        setOpen(false);
        flushFile();
        setShowParentBtns(true);
        setShowScan(false)
        setShowCloudBtn(false)
    }

    React.useEffect(() => {
        if (open) {
        const { current: descriptionElement } = descriptionElementRef;
        if (descriptionElement !== null) {
            descriptionElement.focus();
        }
        }
    }, [open]);

    function macAddress(e) {
        setMac(e.target.value);
    }

    function backFunction() {
        if (showCloudBtn) {
            flushFile();
            setShowCloudBtn(false);
            return;
        }
        if (showScan) {
            flushFile();
            setShowScan(false);
            return;
        }
        if (!showParentBtns) {
            setShowParentBtns(true);
        }
    }

    function onLocalClick(){
        setShowCloudBtn(true);
    }
    
    return (
        <div>
            <BootstrapTooltip classname="text" title="Incubator Registration">
                <div className="new_entry_title_icon" onClick={handleClickOpen()}>
                    <p className="text new_entry_title">New Entry</p>
                    <EventNoteIcon className="eventNote_icon" />
                </div>
            </BootstrapTooltip>
            <Dialog open={open} scroll={scroll} className="new_entry_model_width">
                <div className="add_incube_title">
                    <DialogTitle id="scroll-dialog-title">
                        <Grid container>
                            <Grid item xs={12} sm={3} md={3}>
                                {!showParentBtns && (
                                    <div className="go_back_btn__text_center">
                                        <div className="go_back_btn_icon">
                                            <>
                                                <ArrowBackIcon
                                                className="go_back_btn__icon"
                                                onClick={() => backFunction()}
                                                />
                                            </>
                                        </div>
                                    </div>
                                )}
                            </Grid>
                            <Grid item xs={12} sm={6} md={6}>
                                <div className="title_add_incube">
                                    <span className="text">Add Incubator Tray Here</span>
                                </div>
                            </Grid>
                            <Grid item xs={12} sm={3} md={3}>
                                <div className="cancel_btn__text_center">
                                    <div className="cancel_btn_icon" onClick={() => handleClose()}>
                                        <ClearSharpIcon className="cancel_back_btn__icon" />
                                    </div>
                                </div>
                            </Grid>
                        </Grid>
                    </DialogTitle>
                </div>
                <DialogContent dividers={scroll === "paper"}>
                    <DialogContentText className="text">
                        {showParentBtns && (
                            <Grid container spacing={2}>
                                <Grid item xs={12} sm={6} md={6} className="btn_text">
                                    <div>
                                        <label htmlFor="contained-button-file" onClick={() => {setShowParentBtns(!showParentBtns); setShowTrayType('incubator')}} >
                                            <Button variant="contained" className="btn_box_shadow">
                                                <span className="text">Add Incubator</span>
                                            </Button>
                                        </label>
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6} className="btn_text">
                                    <div>
                                        <label htmlFor="contained-button-file" onClick={() => {setShowParentBtns(!showParentBtns); 
                                        setShowTrayType('zone')}}>
                                            <Button variant="contained" className="btn_box_shadow">
                                                <span className="text">Add Zone</span>
                                            </Button>
                                        </label>
                                    </div>
                                </Grid>
                            </Grid>
                        )}
                        {!showParentBtns && !showScan && (
                            <Grid container spacing={2}>
                                <Grid item xs={12} sm={6} md={6} className="btn_text">
                                    <div>
                                        <label htmlFor="contained-button-file" onClick={() => { onScanClick(); }} >
                                            <Button variant="contained" className="btn_box_shadow">
                                                <span className="text">Scan</span>
                                            </Button>
                                        </label>
                                    </div>
                                </Grid>
                                <Grid item xs={12} sm={6} md={6} className="btn_text">
                                    <div>
                                        <label htmlFor="contained-button-file">
                                            <Button variant="contained" className="btn_box_shadow">
                                                <span className="text">Manual</span>
                                            </Button>
                                        </label>
                                    </div>
                                </Grid>
                            </Grid>
                        )}
                        <div className="tabs_line">
                            {showScan && showLoader && (
                                <>
                                    <div className="scan_qr_code_title">
                                        <p className="text">Please Scan The Bar Code </p>
                                    </div>
                                    <div className="scan_qr_code_img">
                                        <Spinner name="ball-spin-fade-loader" value={progress} className="scan" />
                                    </div>
                                </>
                            )}
                            <div className="tabs_line">
                                {showScan && !showLoader && (
                                    <>
                                        <Grid container spacing={2}>
                                            <Grid item xs={12} sm={6} md={6} className="mac_address_title">
                                                <p className="text mac_address_line">Incubator/Tray ID</p>
                                            </Grid>
                                            <div className="form-filed">
                                                <Grid item xs={12} sm={6} md={6}>
                                                    <TextField
                                                        margin="normal"
                                                        fullWidth
                                                        type="text"
                                                        name="Incubator_Tray_Id"
                                                        id="id"
                                                        className="mac_form_filed"
                                                        value={randomNo}
                                                    />
                                                </Grid>
                                            </div>
                                            {showTrayType === "incubator" && (
                                                <>
                                                    <Grid item xs={12} sm={6} md={6} className="mac_address_title" >
                                                        <p className="text mac_address_line">Please Enter Mac Address*</p>
                                                    </Grid>
                                                    <div className="form-filed">
                                                        <Grid item xs={12} sm={6} md={6}>
                                                            <TextField
                                                                margin="normal"
                                                                fullWidth
                                                                type="number"
                                                                name="mac_address"
                                                                id="mac_address"
                                                                onChange={(e) => { macAddress(e) }}
                                                                className="mac_form_filed"
                                                                autoComplete="off"
                                                                onKeyDown={ e => ( e.keyCode === 69 || e.keyCode === 190 ) && e.preventDefault() }
                                                            />
                                                        </Grid>
                                                    </div>
                                                 </>
                                            )}
                                        </Grid>
                                    </>
                                )}
                                {showTrayType === "zone" && !showLoader && showScan &&(
                                    <>
                                        <Grid container spacing={2}>
                                            <Grid item xs={12} sm={6} md={6} className="mac_address_title">
                                                <p className="text mac_address_line">Sensor Package MAC</p>
                                            </Grid>
                                            <div className="form-filed">
                                                <Grid item xs={12} sm={6} md={6}>
                                                    <TextField
                                                        margin="normal"
                                                        fullWidth
                                                        type="text"
                                                        name="mac_address"
                                                        id="mac_address"
                                                        onChange={(e) => {
                                                            macAddress(e);
                                                        }}
                                                        className="mac_form_filed"
                                                        autoComplete="off"
                                                    />
                                                </Grid>
                                            </div>
                                            <Grid item xs={12} sm={6} md={6} className="mac_address_title" >
                                                <p className="text mac_address_line">TL Fixtures MAC's</p>
                                            </Grid>
                                            <div className="upload_btn">
                                                <input
                                                    accept="csv/*"
                                                    className={classes.input}
                                                    id="contained-button-file"
                                                    multiple
                                                    ref={fRef}
                                                    type="file"
                                                    accept=".csv"
                                                    onChange={(e) => {
                                                        onChangeHandler(e);
                                                    }}
                                                />
                                                <label htmlFor="contained-button-file">
                                                    <Button variant="contained" color="primary" component="span">
                                                        Upload
                                                    </Button>
                                                </label>
                                            </div>
                                        </Grid>
                                    </>
                                )}
                                {showScan && !showLoader && !showCloudBtn && (
                                    <>
                                        <Grid container spacing={2}>
                                            <Grid item xs={12} sm={6} md={6} className="btn_text">
                                                <Button  variant="contained"  className="btn_box_shadow">
                                                    <span className="text">CLOUD</span>
                                                </Button>
                                            </Grid>
                                            <Grid item xs={12} sm={6} md={6} className="btn_text">
                                                <div>
                                                    <label htmlFor="contained-button-file" onClick={() => {onLocalClick();}}>
                                                        <Button variant="contained" className="btn_box_shadow">
                                                            <span className="text">LOCAL</span>
                                                        </Button>
                                                    </label>
                                                </div>
                                            </Grid>
                                        </Grid>
                                    </>
                                )}
                                {showCloudBtn && (
                                    <>
                                        <div className="tabs_width">
                                            <Paper square className="main_tabs">
                                                <Tabs
                                                    value={value}
                                                    onChange={handleChange}
                                                    variant="fullWidth"
                                                    indicatorColor="primary"
                                                    textColor="primary"
                                                    aria-label="icon label tabs example"
                                                    className="tabs_inner_width"
                                                    onClick={handleClick}
                                                >
                                                    <Tab label="UPLOAD" className="text recents" />
                                                    <Tab label="MANUAL" className="text recents" />
                                                </Tabs>
                                            </Paper>
                                        </div>
                                        <div className="upload_csv_msg">
                                            <span className="text error">{errCSVMsg}</span>
                                        </div>
                                        {img && (
                                            <div className="file_upload">
                                                <div>
                                                    <InsertDriveFileIcon className="insertdrivefile_icon" />
                                                </div>
                                                <span>{img}</span>
                                                <div onClick={() => {flushFile();}}>
                                                    <div className="delete_btn">
                                                        <DeleteOutlineIcon className="deleteoutline_icon" />
                                                    </div>
                                                </div>
                                            </div>
                                        )}
                                        <div className="start_btns">
                                            <Button
                                                variant="contained"
                                                // onClick={() => { uploadCSV() }}
                                            >
                                                <span className="text">START</span>
                                            </Button>
                                        </div>
                                    </>
                                )} 
                                <input
                                    accept="csv/*"
                                    className={classes.input}
                                    id="contained-button-file"
                                    multiple
                                    ref={fRef}
                                    type="file"
                                    accept=".csv"
                                    onChange={(e) => {
                                        onChangeHandler(e);
                                    }}
                                />
                            </div>
                        </div>
                    </DialogContentText>
                </DialogContent>
            </Dialog>
        </div>
    );
}