import React from 'react';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import '../../styles/liveImage.css';

const useStyles = makeStyles((theme) =>
    createStyles({
        content: {
            flexGrow: 1,
            height: '100vh',
            overflow: 'none !important',
        },
    }),
);

const useStylesBootstrap = makeStyles((theme) => ({
    arrow: {
      color: theme.palette.common.black,
    },
    tooltip: {
      backgroundColor: theme.palette.common.black,
      fontSize: '15px',
      marginTop: '8px !important',
    },
}));

// function BootstrapTooltip(props) {
//     const classes = useStylesBootstrap();
//     return <Tooltip arrow classes={classes} className="text" {...props} />;
// }

export default function LiveImage(props) {

    console.log(props)
    const classes = useStyles();

    return (
        <div className="title">
            {/* <BootstrapTooltip classname="text" title="Update Parameter's"></BootstrapTooltip> */}
            {(props.inCubeData && props.inCubeData.updated_on) ? (
                <p className="text">{props.inCubeData.updated_on}</p>
                ):(
                <p className="text">Date is not available</p>
            )}
            <div className="card_img_height">
                {( props.showImage && props.inCubeData && props.inCubeData.image_url)? (
                    <img src={props.inCubeData.image_url} alt="Crop Image"  height="345" />
                    ) : (
                    <img src="/assets/image/no_image.png" height="345" />
                )}
            </div>
        </div>
    );
}