import React, { useEffect, useState } from 'react';
import { createStyles, makeStyles, withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { Slider } from '@material-ui/core';
import Tooltip from '@material-ui/core/Tooltip';
import EditIcon from '@material-ui/icons/Edit';
import baseUrl from '../../config/config';
import '../../styles/atmosphereSlider.css';

const useStyles = makeStyles((theme) =>
    createStyles({
        vertical: {
            height: 240,
        },
        horizontal: {
            width: 200,
            margin: '0 auto',
        },
    }),
);

const CustomSlider = withStyles({
    root: {
        height: 8,
        '&$vertical': {
            width: 8,
            marginTop: 4,
        }
    },
    thumb: {
        height: '9px !important',
        width: '90px !important',
        borderRadius: 0,
        backgroundColor: '#fff',
        border: '2px solid rgb(207, 201, 201)',
        marginTop: '-8px !important',
        marginLeft: '0px !important',
        marginBottom: '-5px !important',
        '&:focus, &:hover': {
            boxShadow: 'none'
        },
        '&$active': {
            boxShadow: 'none'
        },
        '& .bar': {
            height: 1,
            width: 85,
            backgroundColor: 'currentColor',
            marginLeft: 1,
            marginRight: 1,
        },
    },
    active: {},
    valueLabel: {
        left: 'calc(-50% + 4px)'
    },
    track: {
        height: 100,
        borderRadius: 4,
    },
    rail: {
        height: 100,
        borderRadius: 4,
    },
    vertical: {
        '& $rail': {
            width: 90,
        },
        '& $track': {
            width: 90,
        },
        '& $thumb': {
            marginLeft: -0,
            marginBottom: -6,
        }
    }
})(Slider);

function CustomThumbComponent(props) {
    return (
        <span {...props}>
            <hr className="bar" />
        </span>
    );
}

const useStylesBootstrap = makeStyles((theme) => ({
    arrow: {
      color: theme.palette.common.black,
    },
    tooltip: {
      backgroundColor: theme.palette.common.black,
      fontSize: '15px',
      marginTop: '8px !important',
      marginLeft: '18px !important',
    },
}))

function BootstrapTooltip(props) {
    const classes = useStylesBootstrap();
    return <Tooltip arrow classes={classes} className="text" {...props} />;
}

export default function AtmosphereSlider(props) {

    const classes = useStyles();

    const [state, setState] = React.useState({
        checkedA: false,
        checkedB: false,
    });

    const [checkedAmanual, setCheckedAmanual] = useState(false);
    const [checkedAauto, setCheckedAauto] = useState(true);

    const [temp, setTemp] = useState(0);
    const [humidity, setHumidity] = useState(0);
    const [co, setCO] = useState(0);
    const [electrolyte, setElectrolyte] = useState(0);
    const [ph, setpH] = useState(0);
    const [isDisabled, setIsDisabled] = useState(false);

    const [autoData, setAutoData] = useState();
    const [manualData, setManualData] = useState();
    const [isActive, setIsActive] = useState(false);

    useEffect(()=>{
        if(props.inCubeData===undefined || props.inCubeData===null){
            setTemp(0);
            setHumidity(0);
            setCO(0);
            setElectrolyte(0);
            setpH(0)
        } else {

        if(props.inCubeData && props.inCubeData){
            setTemp(props.inCubeData.temperature)
        }

        if(props.inCubeData && props.inCubeData){
            setHumidity(props.inCubeData.humidity)
        }

        if(props.inCubeData && props.inCubeData){
            setCO(props.inCubeData.co2)
        }

        if(props.inCubeData && props.inCubeData){
            setElectrolyte(props.inCubeData.electrical_conductivity)
        }

        if(props.inCubeData && props.inCubeData){
            setpH(props.inCubeData.pH)
        }
        setCheckedAauto(props.inCubeData.atmosphere_status);
    }
    },[props.inCubeData])

    useEffect(()=>{
        if (autoData === undefined || (autoData && autoData.error)) {
            setTemp(0);
            setHumidity(0);
            setCO(0);
            setElectrolyte(0);
            setpH(0);
        } else {
            setTemp(autoData.temperature);
            setHumidity(autoData.humidity);
            setCO(autoData.co2);
            setElectrolyte(autoData.electrical_conductivity);
            setpH(autoData.pH);
        }
    },[autoData])

    useEffect(()=>{
        if (manualData === undefined || (manualData && manualData.error)) {
            setTemp(0);
            setHumidity(0);
            setCO(0);
            setElectrolyte(0);
            setpH(0);
        } else {
            setTemp(manualData.temperature);
            setHumidity(manualData.humidity);
            setCO(manualData.co2);
            setElectrolyte(manualData.electrical_conductivity);
            setpH(manualData.pH);
        }
    },[manualData])

    useEffect(() => {
        if (checkedAauto) {
        } else {
        }
        if (checkedAauto) {
          fetchAutoData();
          setIsActive(false)
        }
      }, [checkedAauto]);
    
    useEffect(() => {
        if(checkedAmanual !== null) {
            if (checkedAmanual){
                fetchManualData();
            }
            setCheckedAauto(!checkedAmanual);
        }
    }, [checkedAmanual]);
    
    useEffect(() => {
        if (checkedAauto !== null) 
        setCheckedAmanual(!checkedAauto);
    }, [checkedAauto]);

    function onChangeTemp(e, v) {
        setTemp(v);
    }

    function onChangeHumidity(e, v) {  
        Number(setHumidity(v));
    }

    function onChangeCO(e, v) {
        setCO(v);
    }

    function onChangeElectrolyte(e, v) {
        setElectrolyte(v);
    }

    function onChangepH(e, v) {
        setpH(v);
    }

    // code for run button functionality
    
    function postData(key, value) {
       
        if(key === "electrolyte"){
            props.setInCubeData({...props.inCubeData, electrical_conductivity: electrolyte})
        }
        if(key === "temperature"){
            props.setInCubeData({...props.inCubeData, temperature: temp})
        }
        if(key === "humidity"){
            props.setInCubeData({...props.inCubeData, humidity: humidity})
        }
        if(key === "ph"){
            props.setInCubeData({...props.inCubeData, pH: ph})
        }
        if(key === "co2"){
            props.setInCubeData({...props.inCubeData, co2:co})
        }
    }

    function fetchAutoData(){
        var token = localStorage.getItem('token')
        var selectedId = localStorage.getItem('selectedId')
        var postAutoData = {
            "incubator_id": selectedId
        }
        fetch(`${baseUrl.baseUrl}atmosphericauto/`, { 
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: token,
            },
            body: JSON.stringify(postAutoData)
        })
        .then((res) => res.json())
        .then((results) => {
            setAutoData(results);
        });
    }

    function fetchManualData(){
        var token = localStorage.getItem('token')
        var selectedId = localStorage.getItem('selectedId')
        var postManualData = {
            "incubator_id": selectedId
        }
        fetch(`${baseUrl.baseUrl}atmosphericmanual/`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: token,
            },
            body: JSON.stringify(postManualData)
        })
        .then((res) => res.json())
        .then((results) => {
            if(results.error){
                setCheckedAmanual(false)
                setCheckedAauto(true)
            }
            setManualData(results);
            if(selectedId){
                props.setSelectedID("") 
                props.setSelectedID(selectedId) 
            }
        });
    }

    const onChangeCommitted = (event, key)=>{
        postData(key, event.target.ariaValueNow);
    };

    useEffect(()=>{
        var togglebar = localStorage.getItem('manualToggle')
        if(togglebar==='true'){
            setCheckedAmanual(true)
        } else {
            setCheckedAmanual(false)
        }
    },[])

    useEffect(()=>{
        var togglebar = localStorage.setItem('manualToggle', checkedAmanual)
    },[checkedAmanual])

    useEffect(()=>{
        if(isActive){
            setIsDisabled(false)
            props.setDelayEdit(true)
            props.setAtmShowPen(true)
        }
        else {
            setIsDisabled(true);
        }
    },[isActive]);

    useEffect(()=>{
        setIsActive(props.showAtmPen)
    },[props.showAtmPen]);

    return (
        <div className="atmosphereSlider_main_content">
            <Grid container>
                <div className="atmosphereSlider_content">
                    <Grid item xs={2} sm={2} md={2} lg={2}>
                        <Typography className="temp">
                            <span className="temp_title text">Temp</span>
                        </Typography>
                        <div className={classes.vertical}>
                            <CustomSlider
                                ThumbComponent={CustomThumbComponent}
                                orientation="vertical"
                                defaultValue={temp}
                                onChange={onChangeTemp}
                                onChangeCommitted={(e)=>{
                                    onChangeCommitted(e, 'temperature')
                                }}
                                valueLabelDisplay="auto"
                                className="slider_1"
                                max={150}
                                min={-40}
                                value={temp}
                                disabled={isDisabled}
                            />
                        </div>
                        <div>
                            <Button className="button text" variant="contained">
                                {temp}F
                            </Button>
                        </div>
                    </Grid>
                    <Grid itemitem xs={2} sm={2} md={2} lg={2}>
                        <Typography className="humidity">
                            <span className="humidity_title text">Humidity</span>
                        </Typography>
                        <div className={classes.vertical}>
                            <CustomSlider
                                ThumbComponent={CustomThumbComponent}
                                orientation="vertical"
                                defaultValue={humidity}
                                onChange={onChangeHumidity}
                                onChangeCommitted={(e)=>{
                                    onChangeCommitted(e, 'humidity')
                                }}
                                valueLabelDisplay="auto"
                                value={humidity}
                                className="slider_2"
                                min={0}
                                max={100}
                                disabled={isDisabled}
                            />
                        </div>
                        <div>
                            <Button className="button text" variant="contained">
                                {humidity}%
                            </Button>
                        </div>
                    </Grid>
                    <Grid item xs={2} sm={2} md={2} lg={2}>
                        <Typography className="CO2">
                            <span className="CO2_title text">CO</span>
                        </Typography>
                        <div className={classes.vertical}>
                            <CustomSlider
                                ThumbComponent={CustomThumbComponent}
                                orientation="vertical"
                                defaultValue={co}
                                onChange={onChangeCO}
                                onChangeCommitted={(e)=>{
                                    onChangeCommitted(e,'co2')
                                }}
                                valueLabelDisplay="auto"
                                className="slider_3"
                                min={0}
                                max={100}
                                value={co}
                                disabled={isDisabled}
                            />
                        </div>
                        <div>
                            <Button className="button text" variant="contained">
                                {co}%
                            </Button>
                        </div>
                    </Grid>
                    <Grid item xs={2} sm={2} md={2} lg={2}>
                        <Typography className="electrolyte">
                            <span className="electrolyte_title text">Electrolyte</span>
                        </Typography>
                        <div className={classes.vertical}>
                            <CustomSlider
                                ThumbComponent={CustomThumbComponent}
                                orientation="vertical"
                                defaultValue={electrolyte}
                                onChange={onChangeElectrolyte}
                                onChangeCommitted={(e)=>{
                                    onChangeCommitted(e, 'electrolyte')
                                }}
                                valueLabelDisplay="auto"
                                className="slider_4"
                                value={electrolyte}
                                min={0}
                                max={10}
                                disabled={isDisabled}
                            />
                        </div>
                        <div>
                            <Button className="button text" variant="contained">
                                {electrolyte}
                            </Button>
                        </div>
                    </Grid>
                    <Grid item xs={2} sm={2} md={2} lg={2}>
                        <Typography className="pH">
                            <span className="pH_title text">pH</span>
                        </Typography>
                        <div className={classes.vertical}>
                            <CustomSlider
                                ThumbComponent={CustomThumbComponent}
                                orientation="vertical"
                                defaultValue={ph}
                                onChange={onChangepH}
                                onChangeCommitted={(e)=>{
                                    onChangeCommitted(e, 'ph')
                                }}
                                valueLabelDisplay="auto"
                                className="slider_5"
                                value={ph}
                                min={0}
                                max={14}
                                disabled={isDisabled}
                            />
                        </div>
                        <div>
                            <Button className="button text" variant="contained">
                                {ph}
                            </Button>
                        </div>
                    </Grid>
                </div>
                <div className="atmosphere_titles text">
                    <p>ATMOSPHERE</p>
                </div>
                <Grid container>
                    <div className="atmosphereSlider_controls">
                        <Grid item sm={2} md={2} lg={2}>
                            <div>
                                <label className="switch">
                                    <input type="checkbox" 
                                        checked={checkedAmanual}
                                        name="checkedA"
                                        color="primary"
                                        onChange={() => {
                                            setCheckedAmanual(!checkedAmanual);
                                        }}
                                        inputProps={{ "aria-label": "primary checkbox" }} 
                                    />
                                    <span className="sliders round" />
                                </label>
                            </div>
                        </Grid>
                        <Grid item sm={2} md={2} lg={2}>
                            <div className="manual_title">
                                <Typography className="text" color={checkedAmanual ? "primary" : ""}>
                                    MANUAL
                                </Typography>
                            </div>
                        </Grid>
                        <Grid item sm={2} md={2} lg={2}>
                            <div className="switch_toggle_atmosphere">
                                <label className="switch">
                                     <input type="checkbox" 
                                        checked={checkedAauto}
                                        name="checkedB"
                                        color="primary"
                                        onChange={() => {
                                            setCheckedAauto(!checkedAauto);
                                        }}
                                        inputProps={{ "aria-label": "primary checkbox" }} 
                                    />
                                    <span className="sliders round" />
                                </label>
                            </div>
                        </Grid>
                        <Grid item sm={2} md={2} lg={2}>
                            <div className="auto_title_line">
                                <Typography className="text" color={checkedAauto ? "primary" : ""}>
                                    AUTO
                                </Typography>
                            </div>
                        </Grid>
                        <Grid item sm={2} md={2} lg={2}>
                            {checkedAmanual === true && (
                                <div>
                                    <BootstrapTooltip classname="text" title="Edit Program">
                                        <div className="atmosphere_edit_btn_color">
                                            <div className="atmosphere_round_edit_icon">
                                                <EditIcon onClick={()=>{setIsActive(s=>!s)}} className="atmosphere_graph_edit_icon" style={{ color: isActive ? "white" : "" }} />
                                            </div>
                                        </div>
                                    </BootstrapTooltip>             
                                </div>
                            )}
                        </Grid>
                    </div>
                </Grid>
            </Grid>
        </div>
    );
}      